'use strict';

var jQuery = require('jquery');

var DefinitionManager = require('./../../../../../src/bundles/content/component/DefinitionManager');
var ContentManager = require('./../../../../../src/bundles/content/component/ContentManager');
var ContentSet = require('./../../../../../src/bundles/content/model/ContentSet');
var ContentDataGenerator = require('./../../../helpers/contentDataGenerator');

QUnit.module('Test bundle content: Model ContentSet');

QUnit.test('Test ContentSet construction', function (assert) {

    jQuery('#qunit-fixture').html('<div id="cloud-ui"><div class="bb-content"></div></div>');

    var config = {
            'uid': 'foo',
            'type': 'bar',
            '$': jQuery('.bb-content'),
            'definition': {
                'is_container': true,
                'properties': {}
            }
        },
        content = new ContentSet(config);

    assert.deepEqual(content.uid, config.uid, 'Uid is the same from the config');
    assert.deepEqual(content.type, config.type, 'Type is the same from the config');
    assert.deepEqual(content.$, config.$, 'jQuery object is the same from the config');
    assert.deepEqual(content.definition, config.definition, 'Definition object is the same from the config');
});

QUnit.test('Test isSavable function', function (assert) {
    var config = {
            'uid': 'foo',
            'type': 'bar',
            'definition': {
                'is_container': true,
                'properties': {}
            }
        },
        content = new ContentSet(config);

    assert.ok(content.isSavable(), 'isSavable function return always true for contentset');
});

QUnit.test('Test getChildren function', function (assert) {

    DefinitionManager.add(ContentDataGenerator.generateDefinitions());

    var $fixture = jQuery('#qunit-fixture'),
        contentSet,
        rootContentSet;

    $fixture.html(
        '<div id="my-site">' +
            '<div class="bb-content rootContentSet" data-bb-identifier="ContentSet(e957e5f2958f1a3452d62bb9abf8e81d)">' +
                '<div class="bb-content" id="contentset" data-bb-identifier="ContentSet(54621b46c1664db5ba7127d8f22aff00)">' +
                    '<div class="container">' +
                        '<div class="row">' +
                            '<div class="col-md-6">' +
                                '<div class="bb-content" id="content" data-bb-identifier="Media/Image(997cbc1e2457408dbdd0d208b13a4de8)"></div>' +
                            '</div>' +
                            '<div class="col-md-6">' +
                                '<div class="bb-content" id="content2" data-bb-identifier="Media/Image(d82c8d1619ad8176d665453cfb2e55f0)"></div>' +
                            '</div>' +
                        '</div>' +
                    '</div>' +
                '</div>' +
            '</div>' +
            '</div>'
    );

    contentSet = ContentManager.getContentByNode($fixture.find('#contentset'));
    rootContentSet = ContentManager.getContentByNode($fixture.find('.rootContentSet'));

    assert.deepEqual(contentSet.getChildren().length, 2, 'Children are retrieved');
    assert.deepEqual(rootContentSet.getChildren().length, 1, 'Children from root ContentSet are retrieved');
});

QUnit.test('Test buildColumn function', function (assert) {
    DefinitionManager.add(ContentDataGenerator.generateDefinitions());

    var $fixture = jQuery('#qunit-fixture'),
        contentSet;

    $fixture.html(
        '<div id="my-site">' +
            '<div class="bb-content rootContentSet" data-bb-identifier="ContentSet(e957e5f2958f1a3452d62bb9abf8e81d)">' +
                '<div class="bb-content" id="contentset" data-bb-identifier="ContentSet(54621b46c1664db5ba7127d8f22aff00)">' +
                    '<div class="container">' +
                        '<div class="row">' +
                            '<div class="col-md-2">' +
                                '<div class="bb-content" id="content" data-bb-identifier="Media/Image(997cbc1e2457408dbdd0d208b13a4de8)"></div>' +
                            '</div>' +
                            '<div class="col-md-5">' +
                                '<div class="bb-content" id="content2" data-bb-identifier="Media/Image(d82c8d1619ad8176d665453cfb2e55f0)"></div>' +
                            '</div>' +
                        '</div>' +
                    '</div>' +
                '</div>' +
            '</div>' +
            '</div>'
    );

    contentSet = ContentManager.getContentByNode($fixture.find('#contentset'));

    contentSet.buildColumn();

    assert.ok(contentSet.$.children('.container').children('.row').children('div').hasClass('col-md-6'), 'Columns are builded');
});



