'use strict';

var fixHeight = require('./../../../../src/services/fixHeight');
var jQuery = require('jquery');

QUnit.module('Test fixHeight service', {
    beforeEach: function () {
        jQuery('#qunit-fixture').html('<div id="cloud-ui"></div>');
    }
});

QUnit.test('FixHeight have default function', function (assert) {
    assert.deepEqual(typeof fixHeight.fix, 'function', 'FixHeight have error function');
});

QUnit.test('FixHeight enable scroll on element', function (assert) {
    var element = jQuery('<div><div class="inner-element" style="height:600px"></div></div>'),
        scroll;

    jQuery("#cloud-ui").append(element);
    fixHeight.fix(element.find('.inner-element'), 200);

    scroll = element.find('.cloud-scroll');

    assert.deepEqual(scroll.length, 1, 'FixHeight have the scroll wrapper');
    assert.deepEqual(scroll.css('maxHeight'), '200px', 'Element is fixed height 200px');
    assert.deepEqual(scroll.css('overflowX'), 'hidden', 'Element scroll x is disabled');
    assert.deepEqual(scroll.css('overflowY'), 'auto', 'Element scroll y is enabled');
});



