'use strict';

const jQuery = require('jquery');
const _ = require('lodash');

require('jquery-ui/sortable');

const mediator = require('./../../../../core/mediator');
const container = require('./../../../../core/container');
const Translator = require('./../../../../core/translator');

const FormBuilder = require('./../../builder');
const ContentManager = require('./../../../../bundles/content/component/ContentManager');
const ContentRepository = require('./../../../../bundles/content/repository/ContentRepository');
const SaveManager = require('./../../../../bundles/content/component/SaveManager');
const Uploader = require('./../../../../bundles/content/service/uploader');

require('./../../../../services/dropzone');

const Loader = require('./../../../../services/loader');
let ElementClass = require('./../../elementClass');

let templateEditContent = require('./template/content_edit_toolbar.hbs');
let dropzoneTpl = require('./template/dropzone.hbs');

let uploadedFiles = 0;

const uploadFormats = function(file) {
    let promises = [];
    let sizes = {
            '480': 'image_small',
            '960': 'image_medium',
        };

    for (let i in sizes) {
        if (sizes.hasOwnProperty(i)) {
            promises.push(Uploader.upload(file, {
                'options': {
                    'maxWidth': parseInt(i, 10),
                },
                'extra': {
                    'param_name': sizes[i],
                },
                'dispatchEvent': false,
            }));
        }
    }

    return jQuery.when.apply(undefined, promises).promise();
};

FormBuilder.register('imageList', function(config) {
    let self = new ElementClass(config);
    let $dropzone;
    let currentElements;
    let targetElement;
    let $imagesDiv;
    let maxEntry = config.maxentry || 99;

    self.onSuccess = function(file, response) {
        let context = this;
        let content = config.form.content;
        let $imgListContent = config.form.$;

        uploadedFiles = uploadedFiles + 1;

        Loader.start(config.form.$.parents('.cloud-dialog:first'));

        ContentManager.createElement('Media/Image').done(function(image) {
            Uploader.upload(file, {'dispatchEvent': false, 'save': false}).done(function(response) {
                image.setElements({
                    'path': response.path,
                    'originalname': response.originalname,
                });

                uploadFormats(file).done(function(...responses) {
                    for (let response of responses) {
                        image.addParameter(response.config.extra.param_name, {
                            'path': response.path,
                            'originalname': response.originalname,
                        });
                    }

                    let imageElements = content.getElement(targetElement);

                    imageElements = _.isArray(imageElements) ? imageElements : (imageElements ? [imageElements] : []);

                    imageElements.push({'uid': image.uid, 'type': image.type});

                    content.addElement(targetElement, imageElements);

                    SaveManager.save().done(function() {
                        image.getData().done(function() {
                            context.removeFile(file);

                            self.buildImages(image);

                            uploadedFiles = uploadedFiles - 1;
                            if (uploadedFiles === 0) {
                                Loader.stop(config.form.$.parents('.cloud-dialog:first'));
                                $imgListContent.show();
                            }
                        });
                    });
                });
            });
        });
    };

    self.buildImages = function(images) {
        let $div;
        let parent = config.form.content;
        let imageDomain = container.get('$toolbar').data('image-domain');

        if (imageDomain) {
            imageDomain = imageDomain + (imageDomain.slice(-1) !== '/' ? '/' : '');
        }

        images = _.isArray(images) ? images : (images ? [images] : []);

        _.forEach(images, function(image) {
            if ($imagesDiv.children().not('.dropzone').length >= maxEntry) {
                return;
            }

            $div = jQuery('<div class="item"><div class="figure"></div></div>');

            $div.attr('data-uid', image.uid);
            $div.attr('data-type', image.type);

            $div.find('.figure').css('background-image', 'url(' + imageDomain + image.data.elements.path + ')');

            $div.on(container.get('isMobile') ? 'touchstart MSPointerDown': 'mouseover', function(content) {
                let $current = jQuery(this);
                let $figure = $current.find('.figure');
                let $edit = $figure.find('.cloud-edit-content');

                if ($edit.length === 0) {
                    $edit = jQuery(templateEditContent());
                    $figure.append($edit);

                    $edit.find('.btn-remove').on(container.get('isMobile') ? 'touchstart MSPointerDown': 'click', function(content, parent) {
                        let cElements = parent.getElement(targetElement);
                        let elements = [];

                        _.forEach(cElements, function(element) {
                            if (element.uid !== content.uid) {
                                elements.push(element);
                            }
                        });

                        $current.remove();

                        parent.addElement(targetElement, elements);

                        $dropzone.removeClass('cloud-hidden');

                        SaveManager.save();
                    }.bind($current, content, parent));

                    $edit.find('.btn-parameter').on(container.get('isMobile') ? 'touchstart MSPointerDown': 'click', function(content) {
                        let currentTab = config.form.$.find('.nav-tabs').find('li.active').attr('data-tab');

                        if (container.get('isMobile')) {
                            mediator.publish('menu:configure-content:click', {
                                'params': {
                                    'content': content,
                                    'popin_style': 'addLayer',
                                    'parent': parent,
                                    'callback': function() {
                                        setTimeout(function() {
                                            mediator.publish('menu:configure-content:click', {
                                                'params': {
                                                    'content': parent,
                                                    'popin_style': 'addLayer',
                                                    'current_tab': currentTab,
                                                },
                                            });
                                        }, 1000);
                                    },
                                },
                            });
                        } else {
                            content.preventOverlay = true;
                            mediator.publish('content:plugin:parameter', content);
                        }
                    }.bind($current, content));
                } else {
                    $edit.removeClass('cloud-hidden');
                }
            }.bind($div, image));

            $div.insertBefore($dropzone);
        });

        if ($imagesDiv.children().not('.dropzone').length >= maxEntry) {
            $dropzone.addClass('cloud-hidden');
        }
    };

    self.buildCurrentImages = function() {
        let content = config.form.content;
        let uids = [];
        let images = content.getElement(targetElement);

        images = _.isArray(images) ? images : (images ? [images] : []);

        for (let key in images) {
            if (images.hasOwnProperty(key)) {
                uids.push(images[key].uid);
            }
        }

        if (uids.length > 0) {
            ContentRepository.findByUids(uids).done(function(contents) {
                let elements = [];
                let element;

                _.forEach(uids, function(uid) {
                    element = _.find(contents, {'uid': uid});

                    if (element) {
                        elements.push(element);
                    }
                });

                if (_.isEmpty(contents)) {
                    $dropzone.removeClass('cloud-hidden');
                }

                self.buildImages(elements);
            });
        }
    };

    self.render = function() {
        let $wrapper = jQuery('<div class="image-wrapper"></div>');
        let content = config.form.content;

        $imagesDiv = jQuery('<div class="image-list"></div>');

        targetElement = config.target_element;

        currentElements = _.clone(content.getElement(targetElement));

        $dropzone = jQuery(dropzoneTpl());

        $dropzone.dropzone({
            'init': function() {
                this.on('error', function(file, response) {
                    jQuery(file.previewElement).find('.dz-error-message').text(Translator.translate('upload_image_too_large_message'));
                });
            },
            'url': '/rest/2/resource/upload',
            'sending': function(file, xhr) {
                let $imgListContent = config.form.$;
                $imgListContent.hide();

                let state = require('./../../../auth').getState();

                xhr.setRequestHeader('X-API-KEY', state['X-API-KEY']);
                xhr.setRequestHeader('X-API-SIGNATURE', state['X-API-SIGNATURE']);

                return file;
            },
            'acceptedFiles': 'image/*',
            'success': self.onSuccess,
        });

        self.buildCurrentImages();

        if (maxEntry > 1) {
            $imagesDiv.sortable({
                'containment': 'parent',
                'items': '.item',
                'tolerance': 'pointer',
            });
        } else {
            if (currentElements) {
                $dropzone.addClass('cloud-hidden');
            }
        }

        config.form.on('cancel', function() {
            content.addElement(targetElement, currentElements);
            SaveManager.save();
        });

        config.form.on('submit.parameters', function() {
            let $elements = $imagesDiv.find('.item');
            let elements = [];

            $elements.each(function() {
                let $current = jQuery(this);

                elements.push({
                    'uid': $current.attr('data-uid'),
                    'type': $current.attr('data-type'),
                });
            });

            content.addElement(targetElement, elements);
        });

        $imagesDiv.append($dropzone);
        $wrapper.append($imagesDiv);

        return $wrapper;
    };

    self.getData = function() {
        return null;
    };

    return self;
});



