'use strict';

const jQuery = require('jquery');
const _ = require('lodash');
const GoogleMapsLoader = require('google-maps');

const FormBuilder = require('./../../builder');

let ElementClass = require('./../../elementClass');

let tpl = require('./template/input.hbs');
let itemTpl = require('./template/item.hbs');

GoogleMapsLoader.LIBRARIES = ['places'];

let result = {};

FormBuilder.register('map', function(config) {
    let self = {};

    GoogleMapsLoader.KEY = config.api_key;

    self = new ElementClass(config);

    result = config.value || {};

    self.render = function() {
        let $element = jQuery(tpl());
        let $input = $element.find('input');
        let $ul = $element.find('ul');

        if (config.value.label) {
            $input.val(config.value.label);
        }

        GoogleMapsLoader.load(function(google) {
            let autocomplete = new google.maps.places.AutocompleteService();
            let geoCoder = new google.maps.Geocoder();

            $input.on('input', function() {
                $ul.html('');

                if (!_.isEmpty($input.val())) {
                    autocomplete.getPlacePredictions({
                        'input': $input.val(),
                        'types': [],
                    }, function(data) {
                        _.forEach(data, function(item) {
                            let $item = jQuery(itemTpl(item));

                            $item.on('click', function() {
                                result = {};
                                result.label = jQuery(this).find('.label').text();

                                geoCoder.geocode({
                                    'address': result.label,
                                }, function(geoResult, status) {
                                    if (google.maps.GeocoderStatus.OK === status) {
                                        result.lat = geoResult[0].geometry.location.lat();
                                        result.lng = geoResult[0].geometry.location.lng();
                                    }
                                });

                                $input.val(result.label);
                                $ul.html('');
                            });

                            $ul.append($item);
                        });
                    });
                }
            });
        });

        return $element;
    };

    self.getData = function() {
        return result || {};
    };

    return self;
});



