'use strict';

var _ = require('lodash');
var ContentContainer = require('./../../../../../src/bundles/content/component/ContentContainer');
var Content = require('./../../../../../src/bundles/content/model/Content');

QUnit.module('Test bundle content: Component - ContentContainer');

QUnit.test('ContentManager have default functions', function (assert) {
    assert.ok(typeof ContentContainer.add, 'function', 'Content container have add function');
    assert.ok(typeof ContentContainer.remove, 'function', 'Content container have remove function');
    assert.ok(typeof ContentContainer.get, 'function', 'Content container have get function');
    assert.ok(typeof ContentContainer.getAll, 'function', 'Content container have getAll function');
});

QUnit.test('Add function throw error if element is not an instance of AbstractContent', function (assert) {
    assert.throws(ContentContainer.add, new Error('Element must be an instance of AbstractContent'), 'Throw an error when element is not set');
});

QUnit.test('Test remove function', function (assert) {
    assert.throws(ContentContainer.remove, new Error('You must set the element or the id to remove'), 'Remove function throw error when element is not passed');
});

QUnit.test('Test add / get / remove / getAll functions', function (assert) {

    ContentContainer.clear();

    var config = {
            'type': 'Article',
            'uid': 'bar',
            'definition': {}
        },
        content = new Content(config);

    ContentContainer.add(content);

    assert.deepEqual(ContentContainer.get(content.id), content, 'Add / Get function work');
    assert.deepEqual(_.size(ContentContainer.getAll()), 1, 'Get All function work');

    ContentContainer.remove(content);

    assert.deepEqual(ContentContainer.get(content.id), null, 'Remove function work');
});

QUnit.test('Test clear function', function (assert) {
    ContentContainer.clear();

    assert.deepEqual(_.size(ContentContainer.getAll()), 0, 'Clear function work');
});


