'use strict';

const _ = require('lodash');

let catalogs = {};
let localeFallback = 'en';
let locale = localeFallback;

catalogs.en = require('../i18n/en_EN');
catalogs.fr = require('../i18n/fr_FR');

const availableLangs = ['fr', 'en'];

/**
 * Translator allow to change locale, translate sentence
 *
 * @module core/translator
 * @requires lodash
 * @author Julien Troll <julien.trolle@lp-digital.fr>
 */
module.exports = {
  /**
   * Initialize the user lang and define the locale
   */
  init: function() {
      const userLang = navigator.language || navigator.userLanguage;
      const storage = localStorage.getItem('locale');

      if (!storage) {
          if (userLang) {
              let lang = userLang.substring(0, 2);
              this.setLocale(availableLangs.indexOf(lang) !== -1 ? lang : localeFallback);
          } else {
              this.setLocale(localeFallback);
          }
      } else {
          this.setLocale(storage);
      }
  },

  /**
   * Return the current locale
   *
   * @return {String} locale
   */
  getLocale: function() {
      return locale;
  },

  /**
   * Set the locale and put in the local storage
   *
   * @param {String} l
   */
  setLocale: function(l) {
      locale = l;
      localStorage.setItem('locale', locale);
  },

  /**
   * Set the catalog
   *
   * @param {String} lang
   * @param {Object} data
   */
  setCatalog: function(lang, data) {
      catalogs[lang] = data;
  },

  /**
   * Translate the sentence by a key and replace parameters
   *
   * @param  {String} key
   * @param  {Object} parameters
   * @return {String} value
   */
  translate: function(key, parameters) {
      let value = key;

      if (catalogs[locale] !== undefined && catalogs[locale][key] !== undefined) {
          value = catalogs[locale][key];
      }

      if (parameters !== undefined) {
          value = this.replace(value, parameters);
      }

      return value;
  },

  /**
   * Add keys/values in the catalag
   *
   * @param {Object} data
   * @param {String} lang
   */
  addTransKeys: function(data, lang) {
      if (catalogs[lang]) {
          if (typeof data === 'object') {
              Object.assign(catalogs[lang], data);
          }
      }
  },

  /**
   * Replace parameters from the sentence
   *
   * @param  {String} sentence
   * @param  {Object} parameters
   * @return {String} sentence
   */
  replace: function(sentence, parameters) {
      if (typeof parameters === 'string') {
          parameters = JSON.parse(parameters);
      }

      _.forEach(parameters, function(parameter, key) {
          sentence = sentence.replace('{' + key + '}', parameter);
      });

      return sentence;
  },
};



