'use strict';

let data = {};

/**
 * Container save, delete all data which you need
 *
 * @module core/container
 * @author Julien Troll <julien.trolle@lp-digital.fr>
 */
module.exports = {
  data,
  /**
   * Get data with key
   *
   * @param  {String} key
   * @return {Mixed} data
   */
  get: function(key) {
      return data[key];
  },

  /**
   * Set data with key, value
   *
   * @param {String} key
   * @param {Mixed} value
   */
  set: function(key, value) {
      data[key] = value;
  },

  /**
   * Unset data from container
   *
   * @param {String} key
   */
  unset: function(key) {
      delete data[key];
  },

  /**
   * Clear container
   */
  clear: function() {
      data = {};
  },
};



