'use strict';

var device = require('./../../../../src/services/device');

QUnit.module('Test device service');

QUnit.test('Device have default function', function (assert) {
    assert.deepEqual(typeof device.isMobile, 'function', 'Notifier have error function');
});

QUnit.test('Test isMobile function with desktop', function (assert) {
    assert.notOk(device.isMobile(), 'Desktop is correctly detected');
});

QUnit.test('Test isMobile function with iPhone', function (assert) {

    var userAgent = 'Mozilla/5.0 (iPhone; CPU iPhone OS 7_1_2 like Mac OS X) AppleWebKit/537.51.2 (KHTML, like Gecko) Mobile/11D257';

    assert.ok(device.isMobile(userAgent), 'iPhone is correctly detected');
});

QUnit.test('Test isMobile function with iPad', function (assert) {

    var userAgent = 'Mozilla/5.0 (iPad; CPU OS 8_4_1 like Mac OS X) AppleWebKit/600.1.4 (KHTML, like Gecko) Version/8.0 Mobile/12H321 Safari/600.1.4';

    assert.ok(device.isMobile(userAgent), 'iPad is correctly detected');
});

QUnit.test('Test isMobile function with iPad', function (assert) {

    var userAgent = 'Mozilla/5.0 (iPad; CPU OS 8_4_1 like Mac OS X) AppleWebKit/600.1.4 (KHTML, like Gecko) Version/8.0 Mobile/12H321 Safari/600.1.4';

    assert.ok(device.isMobile(userAgent), 'iPad is correctly detected');
});

QUnit.test('Test isMobile function with Android mobile', function (assert) {

    var userAgent = 'Mozilla/5.0 (Linux; Android 4.2.2; SGH-I547 Build/JDQ39) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/41.0.2272.96 Mobile Safari/537.36';

    assert.ok(device.isMobile(userAgent), 'Android mobile is correctly detected');
});



