'use strict';

var jQuery = require('jquery');
var mediator = require('./../../../src/core/mediator');

require('./../../../src/services/jPlugins');

QUnit.module('jQuery plugins');

QUnit.test('Publish function is regristred and use click event as default', function (assert) {
    var test = false,
        data = {'foo': 'Foo'},
        paramsData = null,
        callback = function () {
            test = true;
            data = paramsData;
        };

    assert.deepEqual(typeof jQuery.fn.publish, 'function', 'Publish is registred to jQuery');

    assert.notDeepEqual(data, paramsData, 'Data and param data is not the same object as default');
    mediator.subscribe('on:body:click', callback);
    jQuery('body').publish('on:body:click', data);
    assert.notOk(test, 'Click is not published');
    jQuery('body').click();
    assert.ok(test, 'Click is published');
    assert.deepEqual(data, paramsData, 'Publish function post parameters');

    test = false;
    data = {'foo': 'Foo'};
    paramsData = null;

    assert.notDeepEqual(data, paramsData, 'Data and param data is not the same object as default');
    mediator.subscribe('on:body:doubleclick', callback);
    jQuery('body').publish('on:body:doubleclick', data, 'dblclick');
    assert.notOk(test, 'Doubleclick is not published');
    jQuery('body').dblclick();
    assert.ok(test, 'Doubleclick is published');
    assert.deepEqual(data, paramsData, 'Publish function post parameters');
});

QUnit.test('Publish function can change the context', function (assert) {
    var test = false,
        data = {'foo': 'Foo'},
        paramsData = null,
        callback = function () {
            test = true;
            data = paramsData;
        };

    assert.notDeepEqual(data, paramsData, 'Data and param data is not the same object as default');
    mediator.subscribe('on:body:doubleclick', callback);
    jQuery('body').publish('on:body:doubleclick', data, 'dblclick');
    assert.notOk(test, 'Doubleclick is not published');
    jQuery('body').dblclick();
    assert.ok(test, 'Doubleclick is published');
    assert.deepEqual(data, paramsData, 'Publish function post parameters');
});


