'use strict';

var jQuery = require('jquery'),
    loader = require('./../../../../src/services/loader'),
    container = require('./../../../../src/core/container'),
    structure = require('./../../../../src/services/structure');

QUnit.module('Test loader service', {
    beforeEach: function () {
        jQuery('#qunit-fixture').html('<div id="cloud-ui"></div>');
        container.set('$toolbar', jQuery('#cloud-ui'));
    },
    afterEach: function () {
        container.clear();
    }
});

QUnit.test('Loader has default functions', function (assert) {
    assert.deepEqual(typeof loader.start, 'function', 'Loader has start function');
    assert.deepEqual(typeof loader.stop, 'function', 'Loader has stop function');
});

QUnit.test('Loader works globally', function (assert) {

    jQuery('#qunit-fixture').html('<div id="site-wrapper"></div>');
    container.set('$siteWrapper', jQuery('#site-wrapper'));

    var $siteWrapper = container.get('$siteWrapper');

    structure.init();

    loader.start();
    assert.deepEqual($siteWrapper.find('.cloud-loading-page').length, 1, 'Loader starts globally');
    assert.deepEqual($siteWrapper.find('.cloud-loading').length, 1, 'Loader starts');
    assert.ok($siteWrapper.find('.cloud-loading').hasClass('rotate'), 'Loader has spinning class');

    loader.stop();
    assert.deepEqual($siteWrapper.find('.cloud-loading-page').length, 0, 'Loader stops globally');
    assert.deepEqual($siteWrapper.find('.cloud-loading').length, 0, 'Loader stops');
    assert.notOk($siteWrapper.find('.cloud-loading').hasClass('rotate'), 'Spinning class element removed');

});

QUnit.test('Loader works per element', function (assert) {

    jQuery('#qunit-fixture').html('<div class="element"></div>');
    var $element = jQuery('.element');

    structure.init();

    loader.start($element);
    assert.deepEqual($element.find('.cloud-loading').length, 1, 'Loader starts on local element');
    assert.ok($element.find('.cloud-loading').hasClass('rotate'), 'Loader has spinning class');

    loader.stop($element);
    assert.deepEqual($element.find('.cloud-loading').length, 0, 'Loader stops on local element');
    assert.notOk($element.find('.cloud-loading').hasClass('rotate'), 'Spinning class element removed');

});


