'use strict';

var jQuery = require('jquery'),
    mediator = require('./../../../src/core/mediator'),
    dnd = require('./../../../src/services/dnd');

require('./../helpers/jquery.simulate.js');

QUnit.module('Test dnd service');

QUnit.test('Dnd have default functions', function (assert) {
    assert.deepEqual(typeof dnd.draggable, 'function', 'Dnd have draggable function');
    assert.deepEqual(typeof dnd.droppable, 'function', 'Dnd have droppable function');
});

QUnit.test('Test apply draggable to element', function (assert) {
    var spyCreate = this.spy(),
        spyDrag = this.spy(),
        spyStart = this.spy(),
        spyStop = this.spy(),
        el;

    jQuery('#qunit-fixture').html('<div id="drag">Drag me</div>');
    el = jQuery('#drag');
    el.css({ width: "10px", height: "10px" });

    mediator.subscribe('on:dragcreate:block', spyCreate);
    mediator.subscribe('on:dragdrag:block', spyDrag);
    mediator.subscribe('on:dragstart:block', spyStart);
    mediator.subscribe('on:dragstop:block', spyStop);

    dnd.draggable(el);

    assert.ok(el.hasClass('ui-draggable'), 'Drag is apply to div');

    el.simulate('drag', {
        dx: 50,
        dy: 55
    });

    assert.ok(spyCreate.called, 'Drag create event dispatched');
    assert.ok(spyDrag.called, 'Drag drag event dispatched');
    assert.ok(spyStart.called, 'Drag start event dispatched');
    assert.ok(spyStop.called, 'Drag stop event dispatched');
});

QUnit.test('Test apply droppable to element', function (assert) {
    var spy = this.spy(),
        dropElement,
        dragElement;

    jQuery('#qunit-fixture').html('<div id="drop"></div><div id="drag">Drag me</div>');
    dropElement = jQuery('#drop');
    dropElement.css({ width: '10px', height: '10px' });
    dragElement = jQuery('#drag');
    dragElement.css({ width: '5px', height: '5px' });

    mediator.subscribe('on:dropcreate:block', spy);

    dnd.draggable(dragElement);
    dnd.droppable(dropElement);

    assert.ok(dropElement.hasClass('ui-droppable'), 'Drag is apply to div');

    dragElement.simulate('drag', {
        'handle': 'corner',
        dx: dropElement.offset().left,
        dy: dropElement.offset().top
    });

    assert.ok(spy.called, 'Drop events dispatched');
});

QUnit.test('Test set of context', function (assert) {
    var spy = this.spy(),
        el;

    jQuery('#qunit-fixture').html('<div id="drag">Drag me</div>');
    el = jQuery('#drag');
    el.css({ width: "10px", height: "10px" });

    mediator.subscribe('on:dragcreate:mycontent', spy);

    dnd.draggable(el, 'mycontent');

    assert.ok(spy.called, 'Context is correctly set');
});

QUnit.test('Test set of option', function (assert) {
    var spy = this.spy(),
        el;

    jQuery('#qunit-fixture').html('<div id="drag">Drag me</div>');
    el = jQuery('#drag');
    el.css({ width: "10px", height: "10px" });

    mediator.subscribe('on:dragcreate:block', spy);

    dnd.draggable(el, null, {
        'axis': 'x'
    });

    assert.ok(spy.called, 'Context is correctly set');
    assert.deepEqual(el.draggable('option', 'axis'), 'x', 'Option is correctly set');
});



