'use strict';

var notifier = require('./../../../../src/services/notifier'),
    jQuery = require('jquery'),
    structure = require('./../../../../src/services/structure'),
    container = require('./../../../../src/core/container');

QUnit.module('Test Notifier service', {
    beforeEach: function () {
        jQuery('#qunit-fixture').html('<div id="cloud-ui"></div>');
        container.set('$toolbar', jQuery('#cloud-ui'));
    },
    afterEach: function () {
        container.clear();
    }
});

QUnit.test('Notifier have default functions', function (assert) {
    assert.deepEqual(typeof notifier.error, 'function', 'Notifier have error function');
    assert.deepEqual(typeof notifier.warning, 'function', 'Notifier have warning function');
    assert.deepEqual(typeof notifier.success, 'function', 'Notifier have success function');
    assert.deepEqual(typeof notifier.info, 'function', 'Notifier have info function');
});

QUnit.test('Test Notification Error creation', function (assert) {
    var message = "notification",
        icon = "icon icon-new-page",
        $notifierWrapper;

    structure.init();

    notifier.error(message, icon);
    $notifierWrapper = container.get('$notifierWrapper');
    assert.deepEqual($notifierWrapper.find('.cloud-notifier').length, 1, 'Notification is created');
    assert.ok($notifierWrapper.find('.cloud-notifier').hasClass('error'), 'Notification with correct class name');
    assert.ok($notifierWrapper.find('.cloud-notifier').find('i').hasClass('icon'), 'Notification with an icon');
    assert.ok($notifierWrapper.find('.cloud-notifier').find('i').hasClass('icon-new-page'), 'Notification with correct icon name');
});

QUnit.test('Test Notification Warning creation', function (assert) {
    var message = "notification",
        $notifierWrapper;

    structure.init();

    notifier.warning(message);
    $notifierWrapper = container.get('$notifierWrapper');
    assert.deepEqual($notifierWrapper.find('.cloud-notifier').length, 1, 'Notification is created');
    assert.ok($notifierWrapper.find('.cloud-notifier').hasClass('warning'), 'Notification with correct class name');
});

