QUnit.test('Test Multiple Notifications', function (assert) {
    var message = "notification",
        $notifierWrapper;

    structure.init();

    notifier.info(message);
    notifier.warning(message);
    $notifierWrapper = container.get('$notifierWrapper');
    assert.deepEqual($notifierWrapper.find('.cloud-notifier').length, 2, 'Two Notifications are created');

});

QUnit.test('Test a notification disappear', function (assert) {
    var message = "notification",
        $notifierWrapper,
        done;

    structure.init();

    notifier.info(message);
    $notifierWrapper = container.get('$notifierWrapper');
    assert.deepEqual($notifierWrapper.find('.cloud-notifier').length, 1, 'Notification is created');
    done = assert.async();
    setTimeout(function () {
        assert.notOk($notifierWrapper.find('.cloud-notifier').length, 'Notification is removed');
        done();
    }, 5500);

});


