'use strict';

var Fb = require('./../../../../../src/services/form');
var jQuery = require('jquery');

module.exports = function (config, formData, assert) {
    var form,
        div = jQuery('<div/>'),
        domForm,
        submitted = false,
        validated = false,
        submitInput;

    form = Fb.getForm(config);

    div.append(form.render());

    domForm = div.find('form:first');

    assert.deepEqual(domForm.length, 1, 'object', 'Form found in DOM');

    assert.deepEqual(domForm.attr('id'), form.id, 'Form id is found');
    assert.ok(domForm.hasClass(form.formClass), 'Form class is found');

    submitInput = domForm.find('button[type=submit]');
    assert.deepEqual(submitInput.length, 1, 'Submit is found');
    assert.ok(submitInput.hasClass(form.elements.submit.elementClass));

    form.on('submit', function () {
        submitted = true;
    });

    form.on('validate', function (data) {
        validated = true;
        if (formData) {
            assert.deepEqual(data, formData, 'Data is same');
        }
    });

    assert.deepEqual(validated, false);
    assert.deepEqual(submitted, false);
    submitInput.click();
    assert.deepEqual(validated, true, 'Validate event is trigged');
    assert.deepEqual(submitted, true, 'Submit event is trigged');

    return {'form': form, 'domForm': domForm};
};


