'use strict';

const jQuery = require('jquery');
const _ = require('lodash');

require('jquery-ui/autocomplete');
require('jquery-ui/sortable');

const Translator = require('./../../../../core/translator');
const RequestHandler = require('./../../../../core/requestHandler');
const Autocomplete = require('./../../../autocomplete');
const Loader = require('./../../../loader');
const FormBuilder = require('./../../builder');

let ElementClass = require('./../../elementClass');
let itemTpl = require('./templates/item.hbs');

const getByUrl = function(url, data) {
    let result;

    url = (url.substring(0, 1) === '/') ? url.substring(1) : url;

    RequestHandler.setBaseUrl('');

    result = RequestHandler.read(url, data);

    RequestHandler.setBaseUrl('/rest/2');

    return result;
};

FormBuilder.register('searchList', function(config) {
    let self = {};
    let $itemWrapper;
    let renderType = config.renderType || 'normal';
    let autocompleteConfig = {
        autocomplete: {
            'source': function(request, response) {
                let $element = config.form.$.find(`.element_${config.name}`);
                let $input = $element.find('.input-autocomplete');

                $input.find('span:first').addClass('cloud-invisible');

                Loader.start($input);
                getByUrl(config.url_request, {
                    'term': request.term,
                }).done(function(data) {
                    $input.find('span:first').removeClass('cloud-invisible');

                    Loader.stop($input);
                    response(data.filter((item) => {
                        return $itemWrapper.find(`[data-id="${item.id}"]`).length === 0;
                    }).map((item) => {
                        return {
                            label: item.label,
                            value: item.value,
                        };
                    }));
                });
            },
            'select': function(event, object) {
                jQuery(event.target).val('').blur();
                self.addItem(object.item);

                return false;
            },
            'minLength': config.minLength || (renderType === 'select' ? 0 : 3),
        },
        icon: (renderType === 'select') ? 'sort-down' : null,
    };

    self = new ElementClass(config);

    self.addItem = function(data) {
        let $item = jQuery(itemTpl());
        let $remove = $item.find('button.remove');

        $item.find('.cloud-label').text(data.label);
        $item.attr('data-id', data.value || data.id);

        $remove.on('click', function() {
            $item.remove();
        });

        $itemWrapper.append($item);
    };

    self.render = function($wrap) {
        let $autocomplete = Autocomplete.render(autocompleteConfig);
        let $input = $autocomplete.find('input[name="autocomplete"]');

        $wrap.addClass('cloud-menu-parameters');

        $itemWrapper = jQuery('<ul class="menu-item-wrapper cloud-list"></ul>');

        $itemWrapper.sortable();

        _.forEach(config.value, function(item) {
            self.addItem(item);
        });

        if (renderType === 'select') {
            let $selectBtn = $input.next('span.autocomplete-search');

            $input.addClass('no-autofocus');
            $input.on('focus', function() {
                if ($input.hasClass('no-autofocus')) {
                    $input.removeClass('no-autofocus');
                    $input.blur();
                } else {
                    $input.autocomplete('search', $input.val());
                }
            });

            $input.on('unfocus', function() {
                $input.autocomplete('close');
            });

            $selectBtn.on('click', function() {
                $input.autocomplete('search', $input.val());
            });
        }

        $wrap.append('<input type="text" class="cloud-hidden"/>');
        $wrap.append('<label class="cloud-label">' + Translator.translate(config.explanation) + '</label>');
        $wrap.append($autocomplete);
        $wrap.append('<div class="cloud-divider"></div>');
        $wrap.append($itemWrapper);

        return;
    };

    self.getData = function() {
        let data = [];

        $itemWrapper.find('.item').each(function() {
            let $current = jQuery(this);

            data.push({
                'id': $current.attr('data-id'),
                'label': $current.find('.cloud-label').text(),
            });
        });

        return data;
    };

    return self;
});



