'use strict';

var navigator = require('./../../../src/services/navigator');

QUnit.module('Test navigator service');

QUnit.test('navigator function exist', function (assert) {
    assert.deepEqual((typeof navigator.getTransition), 'function', 'getTransition function exists');
});

QUnit.test('Test getTransition', function (assert) {
    assert.ok(navigator.getTransition(), 'getTransition not return false if browser is not too old');
});



