'use strict';

var genericTestForm = require('./genericTestForm.spec');

QUnit.module('Form Builder Hidden element Tests');

QUnit.test('Work', function (assert) {
    var config = {
            'elements': {
                'myhidden': {
                    'type': 'hidden',
                    'value': 'foo'
                }
            }
        },
        data = {'myhidden': 'foo'},
        object = genericTestForm(config, data, assert),
        $domForm = object.domForm,
        $wrap = $domForm.find('div.element_myhidden'),
        $input = $wrap.find('input[name="myhidden"]');

    assert.deepEqual($wrap.length, 1, 'Element found');
    assert.deepEqual($input.length, 1, 'Input found');
    assert.deepEqual($input.val(), config.elements.myhidden.value, 'Value found');
});


