'use strict';

var genericTestForm = require('./genericTestForm.spec');

QUnit.module('Form Builder Switch element Tests');

QUnit.test('Work', function (assert) {
    var config = {
            'elements': {
                'myswitch': {
                    'type': 'switch',
                    'label': 'My switch',
                    'wrap_class': 'form-group',
                    'element_class': 'form-control',
                    'label_class': 'foo',
                    'options': {
                        'foo': 'Foo',
                        'bar': 'Bar'
                    },
                    'value': ['bar', 'foo']
                }
            }
        },
        data = {'myswitch': ['foo', 'bar']},
        object = genericTestForm(config, data, assert),
        $domForm = object.domForm,
        $wrap = $domForm.find('div.element_myswitch'),
        $input = $wrap.find('input[name="myswitch"]'),
        $label = $wrap.find('label:first');

    assert.deepEqual($wrap.length, 1, 'Element found');
    assert.ok($input.length > 0, 'Input found');
    assert.deepEqual($label.length, 1, 'Label found');
    assert.ok($wrap.hasClass('element_myswitch ' + config.elements.myswitch.wrap_class), 'Wrap class found');
    assert.ok($input.hasClass(config.elements.myswitch.element_class), 'Input class found');
    assert.ok($label.hasClass(config.elements.myswitch.label_class), 'Label class found');
});

QUnit.test('Work with boolean', function (assert) {
    var config = {
            'elements': {
                'myswitch': {
                    'type': 'switch',
                    'label': 'My switch',
                    'value': true
                }
            }
        },
        data = {'myswitch': true},
        object = genericTestForm(config, data, assert),
        $domForm = object.domForm,
        $wrap = $domForm.find('div.element_myswitch'),
        $input = $wrap.find('input[name="myswitch"]'),
        $label = $wrap.find('label:first');

    assert.deepEqual($wrap.length, 1, 'Element found');
    assert.ok($input.length > 0, 'Input found');
    assert.deepEqual($label.length, 1, 'Label found');
});


