'use strict';

var genericTestForm = require('./genericTestForm.spec');

QUnit.module('Form Builder Radio element Tests');

QUnit.test('Work', function (assert) {
    var config = {
            'elements': {
                'myradio': {
                    'type': 'radio',
                    'wrap_class': 'form-group',
                    'element_class': 'form-control',
                    'label_class': 'foo',
                    'options': {
                        'foo': 'Foo',
                        'bar': 'Bar'
                    },
                    'value': 'bar'
                }
            }
        },
        data = {'myradio': 'bar'},
        object = genericTestForm(config, data, assert),
        $domForm = object.domForm,
        $wrap = $domForm.find('div.element_myradio'),
        $input = $wrap.find('input[name="myradio"]'),
        $label = $wrap.find('label:first');

    assert.deepEqual($wrap.length, 1, 'Element found');
    assert.ok($input.length > 0, 'Input found');
    assert.deepEqual($label.length, 1, 'Label found');
    assert.ok($wrap.hasClass('element_myradio'), 'Basic wrap class found');
    assert.ok($wrap.hasClass(config.elements.myradio.wrap_class), 'Wrap class found');
    assert.ok($input.hasClass(config.elements.myradio.element_class), 'Input class found');
    assert.ok($label.hasClass(config.elements.myradio.label_class), 'Label class found');
});


