'use strict';

var jQuery = require('jquery');

var PopinManager = require('./../../../../../src/services/popin');
var events = require('./../../../../../src/services/popin/popin/events');
var container = require('./../../../../../src/core/container');
var mediator = require('./../../../../../src/core/mediator');

QUnit.module('Test Popover', {
    beforeEach: function () {
        jQuery('#qunit-fixture').html('<div id="cloud-ui"></div>');
    },
    afterEach: function () {
        container.clear();
    }
});

QUnit.test('Test Popover events', function (assert) {

    var testButton = jQuery('<a href="#">Click me</a>'),
        direction = 'left',
        popin;

    container.set('$siteWrapper', jQuery('body'));
    jQuery('#qunit-fixture').append(testButton);

    mediator.subscribe('overlay:click', events.onOverlayClick, true);

    popin = PopinManager.createPopin({
        'dialogClass': 'cloud-dialog cloud-dialog-popover cloud-dialog-popover-' + direction,
        'width': 200,
        'modal': false,
        'minHeight': 100,
        'draggable': false,
        'appendTo' : 'body',
        'position': { my: direction + ' bottom-15', at: direction + ' top', of: testButton },
        'buttons': [
            {
                text: 'delete',
                click: function () {
                    popin.hide();
                }
            },
            {
                text: 'cancel',
                click: function () {
                    popin.hide();
                }
            }
        ]
    });

    popin.setContent('popover');
    popin.display();

    assert.ok(popin.rootElement.is(':visible'), 'Popover is display');

    jQuery('.cloud-overlay').click();

    assert.notOk(popin.rootElement.is(':visible'), 'Popover is destroy');
});



