'use strict';

var genericTestForm = require('./genericTestForm.spec');

QUnit.module('Form Builder Select element Tests');

QUnit.test('Work', function (assert) {
    var config = {
            'elements': {
                'myselect': {
                    'type': 'select',
                    'label': 'My select',
                    'wrap_class': 'form-group',
                    'element_class': 'form-control',
                    'label_class': 'foo',
                    'options': {
                        'foo': 'Foo',
                        'bar': 'Bar'
                    },
                    'value': 'bar'
                }
            }
        },
        data = {'myselect': 'bar'},
        object = genericTestForm(config, data, assert),
        $domForm = object.domForm,
        $wrap = $domForm.find('div.element_myselect'),
        $select = $wrap.find('select[name="myselect"]'),
        $label = $wrap.find('label:first');

    assert.deepEqual($wrap.length, 1, 'Element found');
    assert.deepEqual($select.length, 1, 'Input found');
    assert.deepEqual($label.length, 1, 'Label found');
    assert.ok($wrap.hasClass('element_myselect ' + config.elements.myselect.wrap_class), 'Wrap class found');
    assert.ok($select.hasClass(config.elements.myselect.element_class), 'Input class found');
    assert.ok($label.hasClass(config.elements.myselect.label_class), 'Label class found');
});


