'use strict';

const jQuery = require('jquery');

require('jquery-ui/autocomplete');
const {itemRenderer} = require('./../../../listTree');

const Container = require('./../../../../core/container');
const Translator = require('./../../../../core/translator');
const PageRepository = require('./../../../../bundles/page/repository/page');
const Autocomplete = require('./../../../autocomplete');
const Loader = require('./../../../loader');
const FormBuilder = require('./../../builder');

const deleteBtnTpl = require('./template/delete_button.hbs');
const disableBtnTpl = require('./template/disable_button.hbs');

let ElementClass = require('./../../elementClass');

FormBuilder.register('menu', function(config) {
    let self = new ElementClass(config);
    let $itemWrapper;
    let autocompleteConfig = {
            autocomplete: {
                'source': function(request, response) {
                    let $element = config.form.$.find(`.element_${config.name}`);
                    let $input = $element.find('.input-autocomplete');

                    $input.find('span:first').addClass('cloud-invisible');

                    Loader.start($input);

                    PageRepository.get({
                        'title': '%' + request.term + '%',
                        'page_uid': Container.get('page.uid'),
                    }).done(function(data) {
                        $input.find('span:first').removeClass('cloud-invisible');

                        Loader.stop($input);
                        response(jQuery.map(data, function(item) {
                            return {
                                label: item.title,
                                value: item.url,
                                id: item.id,
                                url: item.url,
                            };
                        }));
                    });
                },
                'select': function(event, object) {
                    jQuery(event.target).val('');

                    $itemWrapper.nestable('add', {
                        'id': object.item.id,
                        'url': object.item.url,
                        'label': object.item.label.replace(/"/g, '\''),
                        'content': object.item.label,
                    });

                    return false;
                },
            },
        };

    self.buildItem = (item) => {
        return {
            'id': item.id,
            'url': item.url,
            'label': item.label.replace(/"/g, '\''),
            'content': item.label,
            'children': item.children,
            'active': !item['is_clickable'] || false,
        };
    };

    self.buildGlobalDisableBtn = function() {
        $itemWrapper.find('> .dd-list').find('> li').each(function() {
            self.buildDisableBtn(jQuery(this));
        });
    };

    self.buildDisableBtn = function($item, active = false) {
        let $handle = $item.find('> .dd-handle');
        let $name = $handle.find('span');
        let $disableBtn = $item.find('input.disable');

        if ($item.find('> .dd-list').children().length > 0) {
            $name.addClass('parent-node');

            if (!$disableBtn.length) {
                let $btn = jQuery(disableBtnTpl({'active': active}));

                $btn.insertAfter($handle);
            }
        } else {
            $name.removeClass('parent-node');
            $disableBtn.remove();
        }
    };

    self.onDeleteBtnClick = function() {
        $itemWrapper.nestable('remove', jQuery(this).parent('li').data('id'));
    };

    self.buildOptions = (data) => {
        return {
            'json': data,
            'maxDepth': 2,
            'callback': self.buildGlobalDisableBtn,
            'itemRenderer': function(...args) {
                let $html = jQuery(itemRenderer(...args));
                let $deleteBtn = jQuery(deleteBtnTpl());

                $deleteBtn.insertAfter($html.find('> .dd-handle'));

                self.buildDisableBtn($html, args[4].active);

                return $html.get(0);
            },
        };
    };

    self.render = function($wrap) {
        let $autocompleteInput = Autocomplete.render(autocompleteConfig);

        $wrap.addClass('cloud-menu-parameters');

        $itemWrapper = jQuery('<div class="dd"></div>');

        $itemWrapper.nestable(self.buildOptions(config.value.map((item) => {
            item.children = item.children && item.children.length ? item.children.map(self.buildItem) : [];

            return self.buildItem(item);
        })));

        $wrap.append('<label class="cloud-label">' + Translator.translate('menu_form_explanation') + '</label>');
        $wrap.append($autocompleteInput);
        $wrap.append('<div class="cloud-divider"></div>');
        $wrap.append($itemWrapper);

        $itemWrapper.on('click', '.btn.remove', self.onDeleteBtnClick);
        self.buildGlobalDisableBtn();
    };

    self.getData = function() {
        const buildData = function(item) {
            let children = item.children && item.children.length ? item.children : null;
            let $item = $itemWrapper.find(`[data-id=${item.id}]`);

            return {
                'id': item.id,
                'url': item.url,
                'label': item.label.replace(/'/g, '\"'),
                'is_clickable': !$item.find('.disable').prop('checked'),
                'children': children ? children.map(buildData) : children,
            };
        };

        return $itemWrapper.nestable('serialize').map(buildData);
    };

    return self;
});



