'use strict';

var Request = require('./../../../src/core/request');

QUnit.module('Request class tests');

QUnit.test('Default object construction', function (assert) {
    var request = new Request(),
        headers = {
            'Content-Type': 'application/x-www-form-uriencoded'
        };

    assert.deepEqual(typeof request.addHeader, 'function', 'Request have addHeader function');
    assert.deepEqual(request.data, null, 'Request have null data');
    assert.deepEqual(typeof request.getContentType, 'function', 'Request have getContentType function');
    assert.deepEqual(typeof request.getData, 'function', 'Request have getData function');
    assert.deepEqual(typeof request.getHeader, 'function', 'Request have getHeader function');
    assert.deepEqual(typeof request.getMethod, 'function', 'Request have getMethod function');
    assert.deepEqual(typeof request.getUrl, 'function', 'Request have getUrl function');
    assert.deepEqual(request.headers, headers, 'Request have application/x-www-form-uriencoded header by default');
    assert.deepEqual(request.method, 'GET', 'Request have GET method');
    assert.deepEqual(typeof request.setContentType, 'function', 'Request have setContentType function');
    assert.deepEqual(typeof request.setData, 'function', 'Request have setData function');
    assert.deepEqual(typeof request.setHeaders, 'function', 'Request have setHeaders function');
    assert.deepEqual(typeof request.setMethod, 'function', 'Request have setMethod function');
    assert.deepEqual(typeof request.setUrl, 'function', 'Request have setUrl function');
    assert.deepEqual(request.url, '', 'Request have an empty url');
});

QUnit.test('Test set/get url functions', function (assert) {
    var request = new Request(),
        url = 'localhost';

    request.setUrl(url);
    assert.deepEqual(request.getUrl(), url, 'Set/Get url works');
});

QUnit.test('Test set/get method functions', function (assert) {
    var request = new Request(),
        method = 'delete';

    request.setMethod(method);
    assert.deepEqual(request.getMethod(), method.toUpperCase(), 'Set/Get method works');
});

QUnit.test('Test set/get data functions', function (assert) {
    var request = new Request(),
        data = {'foo': 'Foo'};

    request.setData(data);
    assert.deepEqual(request.getData(), data, 'Set/Get data works');
});

QUnit.test('Test set/get headers functions', function (assert) {
    var request = new Request(),
        headers = {'Content-Type': 'application/json'};

    request.setHeaders(headers);
    assert.deepEqual(request.getHeaders(), headers, 'Set/Get headers works');
});

QUnit.test('Test set/get header functions', function (assert) {
    var request = new Request(),
        contentType = 'application/json',
        secondHeader = 'Foo';

    request.addHeader('Content-Type', contentType);
    assert.deepEqual(request.getHeader('Content-Type'), contentType, 'Set/Get header works');

    request.addHeader('foo', secondHeader);
    assert.deepEqual(request.getHeader('Content-Type'), contentType, 'First header is not overrided');
    assert.deepEqual(request.getHeader('foo'), secondHeader, 'Second header is set');
});

QUnit.test('Test set/get contentType functions', function (assert) {
    var request = new Request(),
        contentType = 'application/json';

    request.setContentType(contentType);
    assert.deepEqual(request.getContentType(), contentType, 'Set/Get contentType works');
});


