'use strict';

var jQuery = require('jquery');

var authentication = require('./../../../../src/services/auth');
var mediator = require('./../../../../src/core/mediator');
var container = require('./../../../../src/core/container');
var requestHandler = require('./../../../../src/core/requestHandler');

var LogoutListener = require('./../../../../src/bundles/page/listener/logout');

QUnit.module("Testing Authentication Module");

QUnit.test("Test login Event", function (assert) {

    var done = assert.async();

    authentication.authenticate("backbeecloud", "test").done(function () {

        requestHandler.setBaseUrl("backbee");

        assert.deepEqual(JSON.stringify(authentication.getState()), localStorage.getItem(authentication.getStorageKey()), "Authentication state is persisted in localstorage");

        done();

    }).fail(function () {
        assert.ok(false, "Authentication should work");
        done();
    });

});

QUnit.test("Test logout", function (assert) {
    var done = assert.async();

    jQuery('#qunit-fixture').html('<div id="site-wrapper"></div>');
    container.set('$siteWrapper', jQuery('#site-wrapper'));

    requestHandler.setBaseUrl("backbee");

    LogoutListener.onLogout(false, false).done(function () {

        assert.deepEqual(localStorage.getItem(authentication.getStorageKey()), null, "authentication session is cleared");

        done();
    });

});


