'use strict';

var mediator = require('./../../../../src/core/mediator'),
    translator = require('./../../../../src/core/translator'),
    string = require('./template/sample.hbs'),
    stringOneFlag = require('./template/one_flag.hbs'),
    stringMulFlag = require('./template/multi_flag.hbs'),
    Hdl = require('./../../../../src/services/hdlPlugins');

QUnit.module('hdlQuery plugins');

QUnit.test('hdlQuery helpers exist', function (assert) {
    assert.deepEqual((typeof Hdl.helpers.trans), 'function', 'trans helper exists');
});

QUnit.test('hdlQuery trans helper works', function (assert) {
    translator.setLocale('en');

    var stringTranslated = string(),
        stringOneTranslated = stringOneFlag(),
        stringMulTranslated = stringMulFlag();

    assert.deepEqual(stringTranslated, '<p>Yes</p>', 'trans helper works without parameter');
    assert.deepEqual(stringOneTranslated, '<p>There are 4 results</p>', 'trans helper works with one parameter');
    assert.deepEqual(stringMulTranslated, '<p>4/10</p>', 'trans helper works with some parameters');
});



