'use strict';

var genericTestForm = require('./genericTestForm.spec');

QUnit.module('Form Builder Text element Tests');

QUnit.test('Work', function (assert) {
    var config = {
            'elements': {
                'mytext': {
                    'type': 'text',
                    'label': 'My text',
                    'value': 'foo',
                    'wrap_class': 'form-group',
                    'element_class': 'form-control',
                    'label_class': 'foo'
                }
            }
        },
        data = {'mytext': 'foo'},
        object = genericTestForm(config, data, assert),
        $domForm = object.domForm,
        $wrap = $domForm.find('div.element_mytext'),
        $input = $wrap.find('input[name="mytext"]'),
        $label = $wrap.find('label:first');

    assert.deepEqual($wrap.length, 1, 'Element found');
    assert.deepEqual($input.length, 1, 'Input found');
    assert.deepEqual($label.length, 1, 'Label found');
    assert.ok($wrap.hasClass('element_mytext'), 'Basic wrap class found');
    assert.ok($wrap.hasClass(config.elements.mytext.wrap_class), 'Wrap class found');
    assert.ok($input.hasClass(config.elements.mytext.element_class), 'Input class found');
    assert.ok($label.hasClass(config.elements.mytext.label_class), 'Label class found');
    assert.deepEqual($input.val(), config.elements.mytext.value, 'Value found');
});


