QUnit.test('Test basic find function', function (assert) {

    assert.throws(contentRepo.find, new Error('Type or uid missing'), 'find function throw errors if uid or type is not defined');
    assert.throws(contentRepo.find.bind(undefined, 'foo'), new Error('Type or uid missing'), 'find function throw errors if uid or type is not defined');
    assert.throws(contentRepo.find.bind(undefined, undefined, 'bar'), new Error('Type or uid missing'), 'find function throw errors if uid or type is not defined');

    var res = contentRepo.find('Media/Image', '997cbc1e2457408dbdd0d208b13a4de7'),
        done = assert.async();

    res.done(function (content) {
        assert.deepEqual(typeof content, 'object', 'Content is retrieve correctly');

        done();
    }).fail(function () {

        assert.ok(false, 'This test must work');

        done();
    });
});

