QUnit.test('Save function must throw an error when type is not defined', function (assert) {
    assert.throws(contentRepo.save, new Error('Type must be set in ContentRepository::save'), 'error thrown');
});

QUnit.test('Test save function', function (assert) {

    var done = assert.async(),
        done2 = assert.async(),
        data;

    Container.set('page.uid', '123456');

    contentRepo.save({'type': 'Media/Image'}).done(function (data, response) {

        assert.notDeepEqual(response.getHeader('BB-RESOURCE-UID'), null, 'save function as POST work');

        done();

        return data;
    }).fail(function () {

        assert.ok(false, 'This test must work');

        done();
    });

    data = {
        'type': 'Media/Image',
        'uid': 'd21fd909c4ce4c3ee7a6d84bf2bbb044',
        'elements': {
            'title': {
                'uid': '6c5d6d0298aa74976b0a35eba7a68eb4',
                'type': 'Element/Text'
            }
        }
    };

    contentRepo.save(data).done(function (data, response) {

        assert.notDeepEqual(response.getHeader('BB-RESOURCE-UID'), null, 'save function as PUT work');

        done2();

        return data;
    }).fail(function () {
        assert.ok(false, 'This test must work');

        done2();
    });
});


