'use strict';

const jQuery = require('jquery');
const _ = require('lodash');

const Translator = require('./../../../../core/translator');
const Loader = require('./../../../../services/loader');
const Notifier = require('./../../../../services/notifier');
const Autocomplete = require('./../../../autocomplete');
const FormBuilder = require('./../../builder');
const Repository = require('./../../../../bundles/tag/repository/TagRepository');

const templateSelectTag = require('./template/select_tag.hbs');
const templateButton = require('./template/button_tag.hbs');

let ElementClass = require('./../../elementClass');

FormBuilder.register('selectTag', function(config) {
    let self = new ElementClass(config);
    let $autocompleteInput;
    let $createTagWrapper = jQuery('<p class="create-tag cloud-group"></p>');
    let allowAdd = _.isBoolean(config.allow_add) ? config.allow_add : true;
    let $wrapper;

    self.removeTag = function(tagLabel) {
        let btn = $wrapper.find('.buttons').find('.cloud-tag span:contains(' + tagLabel + ')').parent();

        btn.remove();
        $wrapper.find('select option[value="' + tagLabel + '"]').css('display', '');

        _.remove(self.value, {'label': tagLabel});
    };

    self.addTag = function(tag) {
        let $buttonWrapper = $wrapper.find('.buttons');

        self.value = self.value || [];

        if (_.find(self.value, {'label': tag.label})) {
            return;
        }

        let newTag = {};

        newTag.label = tag.label;
        if (tag.uid) {
            newTag.uid = tag.uid;
        }

        self.value.push(newTag);

        $wrapper.find('select option[value="' + tag.label + '"]').css('display', 'none');
        $wrapper.find('select option:eq(0)').prop('selected');
        $buttonWrapper.append(templateButton({'tag': tag}));
    };

    self.onCreateNewTag = function(event) {
        Repository.create(event.data).done(function() {
            $createTagWrapper.html('');
            $autocompleteInput.val('');
            Notifier.success(Translator.translate('tag_created'));
        });
    };

    self.render = function() {
        let value = config.value;
        let buttons = '';
        let autocompleteConfig = {
                autocomplete: {
                    'source': function(request, response) {
                        let $element = config.form.$.find(`.element_${config.name}`);
                        let $input = $element.find('.input-autocomplete');

                        $input.find('span:first').addClass('cloud-invisible');

                        let term = jQuery.trim(request.term.replace(/[\/\\"]/gi, ' ')).replace(/\s+/g, ' ');

                        if (term.length < 2) {
                            $createTagWrapper.html('');
                            jQuery('.ui-autocomplete').css('display', 'none');
                            return;
                        }

                        Loader.start($input);
                        Repository.findBy({'term': term}).done(function(data) {
                            $input.find('span:first').removeClass('cloud-invisible');
                            Loader.stop($input);

                            if (allowAdd) {
                                let createBtn = true;

                                _.forEach(data, function(item) {
                                    if (_.lowerCase(item.keyword) === _.lowerCase(term)) {
                                        createBtn = false;
                                    }
                                });

                                if (createBtn) {
                                    data.push({
                                        'keyword': term,
                                    });
                                }
                            } else {
                                if (data.length > 0) {
                                    $wrapper.find('span.cloud-label.error').remove();
                                } else {
                                    let $message = jQuery('<span class="cloud-label error">'+ Translator.translate('no_tag_message') +'</span>');

                                    if ($wrapper.find('span.cloud-label.error').length ===0) {
                                        $wrapper.find('.cloud-group.cloud-autocomplete').append($message);
                                    }
                                }
                            }

                            if (data.length > 0 || allowAdd) {
                                response(jQuery.map(data, function(item) {
                                    return {
                                        label: item.keyword,
                                        value: item.keyword,
                                        uid: item.uid,
                                    };
                                }));
                            }
                        });
                    },
                    'select': function(event, object) {
                        jQuery(event.target).val('');
                        self.addTag(object.item);

                        return false;
                    },
                    'minLength': 0,
                },
            };

        let $autocomplete;

        if (config.appendTo) {
            autocompleteConfig.autocomplete.appendTo = config.appendTo;
        }

        $autocomplete = Autocomplete.render(autocompleteConfig);

        $autocompleteInput = $autocomplete.find('input');

        _.each(value, function(tag) {
            if (!_.isEmpty(tag)) {
                buttons = buttons + templateButton({'tag': tag});
            }
        });

        $wrapper = jQuery(templateSelectTag({'buttons': buttons, 'label': self.label}));
        $wrapper.find('.auto-wrapper').append($autocomplete);

        $createTagWrapper.insertAfter($autocomplete);

        $wrapper.find('input[type=hidden]').val(value);

        $wrapper.find('.btn-add-tag').on('click', function() {
            self.addTag($wrapper.find('select option:selected').attr('value'));
        });

        $wrapper.find('.buttons').on('click', '.cloud-tag', function(e) {
            e.preventDefault();
            self.removeTag(jQuery(this).find('span').text());
        });

        _.each(jQuery(buttons), function(btn) {
            let tag = jQuery(btn).find('span').text();

            $wrapper.find('select option[value="' + tag + '"]').css('display', 'none');
        });

        return $wrapper;
    };

    self.getData = function() {
        return self.value;
    };

    return self;
});



