const jQuery = require('jquery');

const Dropzone = require('dropzone');

// Shim bug in dropzone
jQuery.fn.dropzone = function(options) {
  return this.each(function() {
    return new Dropzone(this, options);
  });
};

module.exports = Dropzone;



