'use strict';

var Response = require('./../../../src/core/response');

QUnit.module('Response class tests');

QUnit.test('Default object construction', function (assert) {
    var response = new Response();

    assert.deepEqual(response.data, {}, 'Response have an empty object for data');
    assert.deepEqual(response.errorText, '', 'Response have an empty error text');
    assert.deepEqual(response.headers, {}, 'Response have an empty object for headers');
    assert.deepEqual(response.rawData, '', 'Response have an empty raw data');
    assert.deepEqual(response.data, {}, 'Response have an empty object for data');
    assert.deepEqual(response.status, 200, 'Response have a 200 status code by default');
    assert.deepEqual(response.statusText, '', 'Response have an empty status text');
    assert.deepEqual(typeof response.addHeader, 'function', 'Response have addHeader function');
    assert.deepEqual(typeof response.getData, 'function', 'Response have getData function');
    assert.deepEqual(typeof response.getErrorText, 'function', 'Response have getErrorText function');
    assert.deepEqual(typeof response.getHeader, 'function', 'Response have getHeader function');
    assert.deepEqual(typeof response.getHeaders, 'function', 'Response have getHeaders function');
    assert.deepEqual(typeof response.getRangeFrom, 'function', 'Response have getRangeFrom function');
    assert.deepEqual(typeof response.getRangeTo, 'function', 'Response have getRangeTo function');
    assert.deepEqual(typeof response.getRangeTotal, 'function', 'Response have getRangeTotal function');
    assert.deepEqual(typeof response.getRawData, 'function', 'Response have getRawData function');
    assert.deepEqual(typeof response.getStatus, 'function', 'Response have getStatus function');
    assert.deepEqual(typeof response.getStatusText, 'function', 'Response have getStatusText function');
    assert.deepEqual(typeof response.getUidFromLocation, 'function', 'Response have getUidFromLocation function');
    assert.deepEqual(typeof response.setData, 'function', 'Response have setData function');
    assert.deepEqual(typeof response.setErrorText, 'function', 'Response have setErrorText function');
    assert.deepEqual(typeof response.setHeaders, 'function', 'Response have setHeaders function');
    assert.deepEqual(typeof response.setRawData, 'function', 'Response have setRawData function');
    assert.deepEqual(typeof response.setStatus, 'function', 'Response have setStatus function');
    assert.deepEqual(typeof response.setStatusText, 'function', 'Response have setStatus function');
});

QUnit.test('Test add/get header function', function (assert) {
    var response = new Response(),
        contentType = 'application/json';

    assert.deepEqual(response.headers, {}, 'Headers is empty by default');

    response.addHeader('Content-Type', contentType);

    assert.deepEqual(response.getHeader('Content-Type'), contentType, 'add/set function work');
});

QUnit.test('Test set/get headers function', function (assert) {
    var response = new Response(),
        headers = {
            'Content-Type': 'application/json'
        };

    response.setHeaders(headers);

    assert.deepEqual(response.getHeaders(), headers, 'set/get function work');
});

QUnit.test('Test set/get data function', function (assert) {
    var response = new Response(),
        data = {'foo': 'Foo'};

    response.setData(data);

    assert.deepEqual(response.getData(), data, 'set/get function work');
});

QUnit.test('Test set/get errorText function', function (assert) {
    var response = new Response(),
        error = 'An error occured';

    response.setErrorText(error);

    assert.deepEqual(response.getErrorText(), error, 'set/get function work');
});

QUnit.test('Test set/get rawData function', function (assert) {
    var response = new Response(),
        rawData = '{"Foo": "foo"}';

    response.setRawData(rawData);

    assert.deepEqual(response.getRawData(), rawData, 'set/get function work');
});

QUnit.test('Test set/get status function', function (assert) {
    var response = new Response(),
        status = 500;

    response.setStatus(status);

    assert.deepEqual(response.getStatus(), status, 'set/get function work');
});

QUnit.test('Test set/get statusText function', function (assert) {
    var response = new Response(),
        statusText = '500 Internal error';

    response.setStatusText(statusText);

    assert.deepEqual(response.getStatusText(), statusText, 'set/get function work');
});

QUnit.test('Test rangeFrom function without Content-Range', function (assert) {
    var response = new Response();

    assert.deepEqual(response.getRangeFrom(), null, 'Rangefrom is null if Content-Range header doesnt exist');
});

QUnit.test('Test rangeFrom function', function (assert) {
    var response = new Response();

    response.addHeader('Content-Range', '0-31/32');

    assert.deepEqual(response.getRangeFrom(), 0, 'Rangeform function work');
});

QUnit.test('Test rangeTo without Content-Range', function (assert) {
    var response = new Response();

    assert.deepEqual(response.getRangeTo(), null, 'RangeTo is null if Content-Range header doesnt exist');
});

QUnit.test('Test rangeTo function', function (assert) {
    var response = new Response();

    response.addHeader('Content-Range', '0-31/32');

    assert.deepEqual(response.getRangeTo(), 31, 'RangeTo function work');
});

QUnit.test('Test rangeTotal without Content-Range', function (assert) {
    var response = new Response();

    assert.deepEqual(response.getRangeTotal(), null, 'RangeTotal is null if Content-Range header doesnt exist');
});

QUnit.test('Test rangeTotal function', function (assert) {
    var response = new Response();

    response.addHeader('Content-Range', '0-31/32');

    assert.deepEqual(response.getRangeTotal(), 32, 'RangeTotal function work');
});

QUnit.test('Test getUidFromLocation function without Location header', function (assert) {
    var response = new Response();

    assert.deepEqual(response.getUidFromLocation(), null, 'Uid is null if Location header doesnt exist');
});

QUnit.test('Test getUidFromLocation function with bad location', function (assert) {
    var response = new Response();

    response.addHeader('Location', 'foo');

    assert.deepEqual(response.getUidFromLocation(), null, 'Uid is null if Location header is invalid');
});

QUnit.test('Test getUidFromLocation', function (assert) {
    var response = new Response();

    response.addHeader('Location', 'http://foo/cdf458ccda03d0b61a33aaa921c4d376');

    assert.deepEqual(response.getUidFromLocation(), 'cdf458ccda03d0b61a33aaa921c4d376', 'getUidFromLocation function work');
});


