'use strict';

var mediator = require('./../../../src/core/mediator');

QUnit.module('Mediator tests');

QUnit.test('Mediator have default functions', function (assert) {
    assert.deepEqual(typeof mediator.subscribe, 'function', 'Mediator have subscribe function');
    assert.deepEqual(typeof mediator.publish, 'function', 'Mediator have publish function');
});

QUnit.test('Subsribe and publish work', function (assert) {
    var test = false,
        callback = function () {
            test = true;
        };

    assert.deepEqual(test, false, 'First time test equal to false');
    mediator.subscribe('test:event', callback);
    mediator.publish('test:event');
    assert.deepEqual(test, true, 'Callback is called after publish');
});

QUnit.test('Subscribe once work', function (assert) {
    var count = 0,
        callback = function () {
            count = count + 1;
        };

    assert.deepEqual(count, 0, 'First time count equal to 0');
    mediator.subscribe('test:subscribe-once', callback, true);
    mediator.publish('test:subscribe-once');
    assert.deepEqual(count, 1, 'Callback is called after the first publish');
    mediator.publish('test:subscribe-once');
    assert.deepEqual(count, 1, 'Callback is not called after the second publish');
});

QUnit.test('Callback retrieve parameters', function (assert) {
    var data = {
            'foo': 'Foo',
            'bar': {}
        },
        paramData = null,
        callback = function (params) {
            paramData = params;
        };

    assert.notDeepEqual(data, paramData, 'Data and param data is not the same object as default');
    mediator.subscribe('test:param', callback);
    mediator.publish('test:param', data);
    assert.deepEqual(data, paramData, 'Publish function post parameters');
});

QUnit.test('Clear one event work', function (assert) {
    var count = 0,
        count2 = 0,
        callbacks = {
            first: function () {
                count = count + 1;
            },
            second: function () {
                count2 = count2 + 1;
            }
        };

    mediator.subscribe('test:clear', callbacks.first);
    mediator.subscribe('test:clear2', callbacks.second);

    assert.deepEqual(count, 0, 'Callback is not called yet');
    mediator.publish('test:clear');
    assert.deepEqual(count, 1, 'Callback is called');
    mediator.publish('test:clear');
    assert.deepEqual(count, 2, 'Callback is called again');
    mediator.clear('test:clear');
    mediator.publish('test:clear');
    assert.deepEqual(count, 2, 'Callback is not called');

    mediator.publish('test:clear2');
    assert.deepEqual(count2, 1, 'test:clear2 callback is always available');
});

QUnit.test('Clear events array work', function (assert) {
    var count = 0,
        count2 = 0,
        callbacks = {
            first: function () {
                count = count + 1;
            },
            second: function () {
                count2 = count2 + 1;
            }
        };

    mediator.subscribe('test:clear', callbacks.first);
    mediator.subscribe('test:clear2', callbacks.second);

    assert.deepEqual(count, 0, 'Callback is not called yet');
    mediator.publish('test:clear');
    assert.deepEqual(count, 1, 'Callback is called');
    mediator.publish('test:clear');
    assert.deepEqual(count, 2, 'Callback is called again');

    mediator.clear(['test:clear', 'test:clear2']);

    mediator.publish('test:clear');
    assert.deepEqual(count, 2, 'Callback is not called');
    mediator.publish('test:clear2');
    assert.deepEqual(count2, 0, 'Callback is not called');
});

QUnit.test('Clear all work', function (assert) {
    var count = 0,
        count2 = 0,
        callbacks = {
            first: function () {
                count = count + 1;
            },
            second: function () {
                count2 = count2 + 1;
            }
        };

    mediator.subscribe('test:clear', callbacks.first);
    mediator.subscribe('test:clear2', callbacks.second);

    mediator.publish('test:clear');
    assert.deepEqual(count, 1, 'Callback is called');
    mediator.publish('test:clear2');
    assert.deepEqual(count2, 1, 'test:clear2 callback is always available');

    mediator.clear();

    mediator.publish('test:clear');
    mediator.publish('test:clear2');
    assert.deepEqual(count, 1, 'Callback is not called');
    assert.deepEqual(count2, 1, 'Callback is not caled');
});


