'use strict';

var container = require('./../../../src/core/container');

QUnit.module('Container tests', {
    afterEach: function () {
        container.clear();
    }
});

QUnit.test('Container have default functions', function (assert) {
    assert.deepEqual(typeof container.get, 'function', 'Container have get function');
    assert.deepEqual(typeof container.set, 'function', 'Container have set function');
    assert.deepEqual(typeof container.unset, 'function', 'Container have unset function');
    assert.deepEqual(typeof container.clear, 'function', 'Container have clear function');
});

QUnit.test('Get, Set, Unset, clear method work', function (assert) {
    assert.deepEqual(container.get('test'), undefined, 'get returns undefined');
    container.set('test', 0);
    assert.deepEqual(container.get('test'), 0, 'get returns the set value: 0');
    container.set('test', 1);
    assert.deepEqual(container.get('test'), 1, 'get returns the set value: 1');
    container.unset('test');
    assert.deepEqual(container.get('test'), undefined, 'get returns undefined after remove a key');
    container.set('test', 1);
    container.set('test2', 1);
    assert.deepEqual(container.get('test'), 1, 'get returns the set value: 1');
    assert.deepEqual(container.get('test2'), 1, 'get returns the set value: 1');
    container.clear();
    assert.deepEqual(container.get('test'), undefined, 'get returns undefined after clear');
    assert.deepEqual(container.get('test2'), undefined, 'get returns undefined after clear');
});



