'use strict';

var jQuery = require('jquery');

var SaveManager = require('./../../../../../src/bundles/content/component/SaveManager');
var ContentManager = require('./../../../../../src/bundles/content/component/ContentManager');
var ContentContainer = require('./../../../../../src/bundles/content/component/ContentContainer');
var DefinitionManager = require('./../../../../../src/bundles/content/component/DefinitionManager');
var ContentDataGenerator = require('./../../../helpers/contentDataGenerator');

QUnit.module('Test bundle content: Component - SaveManager');

QUnit.test('Save manager have default functions', function (assert) {
    assert.deepEqual(typeof SaveManager.save, 'function', 'Definition manager have save function');
    assert.deepEqual(typeof SaveManager.merge, 'function', 'Definition manager have merge function');
    assert.deepEqual(typeof SaveManager.push, 'function', 'Definition manager have push function');
});


QUnit.test('Test merge function', function (assert) {
    ContentContainer.clear();
    DefinitionManager.add(ContentDataGenerator.generateDefinitions());

    var content =ContentManager.buildElement({
            'uid': 'fd9d11e7706d0271eb3a2c943cb67f7d',
            'type': 'Media/Image'
        }),
        revision = content.revision,
        elements;

    SaveManager.merge(content);

    assert.deepEqual(content.data, null, 'Content is not merged');

    elements = {
        'title': {
            'uid': 'd816eb83a58154e3c34e784ac23aaa66',
            'type': 'Element/Text'
        }
    };

    content.data = {
        'elements': {
            'title': {
                'uid': 'b86944ed39630bc81605a3016753f2e9',
                'type': 'Element/Text'
            }
        }
    };

    content.setElements(elements);

    assert.deepEqual(content.data.elements.title.uid, 'b86944ed39630bc81605a3016753f2e9', 'Title is correctly defined');
    assert.deepEqual(revision.elements.title.uid, 'd816eb83a58154e3c34e784ac23aaa66', 'Revision have a correct title');

    SaveManager.merge(content);

    assert.deepEqual(content.data.elements.title.uid, 'd816eb83a58154e3c34e784ac23aaa66', 'Title is correctly merged');
    assert.deepEqual(revision.elements, null, 'Revision is reset');
    assert.notOk(content.updated, 'Updated is false');

    ContentContainer.clear();
});

