'use strict';

const jQuery = require('jquery');

require('./../../../../services/dropzone');

const container = require('./../../../../core/container');
const Translator = require('./../../../../core/translator');
const FormBuilder = require('./../../builder');
const ElementClass = require('./../../elementClass');

const uploadTpl = require('./template/upload.hbs');
const dropzoneTpl = require('./template/upload_dropzone.hbs');
const previewTpl = require('./template/upload_preview.hbs');

FormBuilder.register('upload', function(config) {
    let self = new ElementClass(config);
    let picto = config.value || '';
    let imageDomain = container.get('$toolbar').data('image-domain');

    if (imageDomain) {
        imageDomain = imageDomain + (imageDomain.slice(-1) !== '/' ? '/' : '');
    }

    self.onSuccess = function(file, response) {
        picto = response.path;
        self.$.find('.cloud-dropzone').remove();
        self.$.find('.cloud-upload').append(self.buildPreview());
    };

    self.buildDropzone = function() {
        let $dropzone = jQuery(dropzoneTpl({
            'label': Translator.translate('browse'),
        }));

        $dropzone.dropzone({
            'url': '/rest/2/resource/upload',
            'maxFiles': 1,
            'uploadMultiple': false,
            'thumbnailHeight': 88,
            'thumbnailWidth': 88,
            'sending': function(file, xhr) {
                let state = require('./../../../auth').getState();

                xhr.setRequestHeader('X-API-KEY', state['X-API-KEY']);
                xhr.setRequestHeader('X-API-SIGNATURE', state['X-API-SIGNATURE']);

                return file;
            },
            'success': self.onSuccess,
        });

        return $dropzone;
    };

    self.onDelete = function() {
        picto = '';
        self.$.find('.cloud-preview').remove();
        self.$.find('.cloud-upload').append(self.buildDropzone());
    };

    self.buildPreview = function() {
        const fileType = picto.split('.').pop();
        const imgExt = ['jpg', 'jpeg', 'png', 'svg', 'gif'];
        let $preview = jQuery(previewTpl());

        $preview.find('.btn-remove').on('click', self.onDelete);

        if (imgExt.indexOf(fileType)) {
            $preview.append('<img src="' + imageDomain + picto + '">');
        } else {
            $preview.text(picto);
        }

        return $preview;
    };

    self.render = function() {
        let $wrapper = jQuery(uploadTpl());

        if (config.description) {
            $wrapper.append(jQuery(`<p>${Translator.translate(config.description)}</p>`));
        }

        $wrapper.append(self[picto === '' ? 'buildDropzone' : 'buildPreview']());

        return $wrapper;
    };

    self.getData = function() {
        return picto;
    };

    return self;
});



