'use strict';

var DefinitionManager = require('./../../../../../src/bundles/content/component/DefinitionManager');
var _ = require('lodash');

QUnit.module('Test bundle content: Component - DefinitionManager');

QUnit.test('Definition manager have default functions', function (assert) {
    assert.ok(typeof DefinitionManager.add, 'function', 'Definition manager have add function');
    assert.ok(typeof DefinitionManager.get, 'function', 'Definition manager have get function');
    assert.ok(typeof DefinitionManager.clear, 'function', 'Definition manager have clear function');
});

QUnit.test('Add function throw error when definitions is not an array', function (assert) {
    assert.throws(DefinitionManager.add, new Error('Definitions must be an array'), 'add function throw error');
});

QUnit.test('Test add / get / getAll function', function (assert) {

    DefinitionManager.clear();

    var definitions = [
            {
                'label': 'Article',
                'type': 'Article',
                'parameters': {}
            },
            {
                'label': 'ContentSet',
                'type': 'ContentSet',
                'parameters': {}
            }
        ];

    DefinitionManager.add(definitions);

    assert.deepEqual(_.size(DefinitionManager.getAll()), 2, 'Add / GetAll function work');
    assert.notDeepEqual(DefinitionManager.get('Article'), null, 'Add / Get function work');

    DefinitionManager.clear();
});

QUnit.test('Test clear function', function (assert) {
    DefinitionManager.clear();

    assert.deepEqual(_.size(DefinitionManager.getAll()), 0, 'Clear function work');
});


