'use strict';

var jQuery = require('jquery');
var requestHandler = require('./../../../src/core/requestHandler');
var Request = require('./../../../src/core/request');
var Response = require('./../../../src/core/response');
var mediator = require('./../../../src/core/mediator');

QUnit.module('Request Handler Tests');

mediator.clear();
requestHandler.setBaseUrl('backbee');

QUnit.test('Default properties', function (assert) {
    assert.deepEqual(typeof requestHandler.create, 'function', 'Db have create function');
    assert.deepEqual(typeof requestHandler.del, 'function', 'Db have del function');
    assert.deepEqual(typeof requestHandler.link, 'function', 'Db have link function');
    assert.deepEqual(typeof requestHandler.patch, 'function', 'Db have patch function');
    assert.deepEqual(typeof requestHandler.read, 'function', 'Db have read function');
    assert.deepEqual(typeof requestHandler.update, 'function', 'Db have update function');
    assert.deepEqual(typeof requestHandler.send, 'function', 'Db have send function');
    assert.deepEqual(typeof requestHandler.setBaseUrl, 'function', 'Db have setBaseUrl function');
    assert.deepEqual(typeof requestHandler.getBaseUrl, 'function', 'Db have getBaseUrl function');
    assert.deepEqual(typeof requestHandler.addSearch, 'function', 'Db have addSearch function');
});

QUnit.test('Test getBaseUrl function', function (assert) {
    assert.deepEqual(requestHandler.getBaseUrl(), 'backbee', 'getBaseUrl function work correctly');
});

QUnit.test('Test send function with bad parameter', function (assert) {
    assert.throws(requestHandler.send, new Error('The parameter must be an instance of Request'), 'Send function throw an error if request param is not valid');
});

QUnit.test('Test send function done', function (assert) {
    var request = new Request(),
        done = assert.async(),
        headers = {'Content-Type': 'application/json'},
        spy = this.spy(),
        spyDone = this.spy();

    mediator.subscribe('request:send:before', spy);
    mediator.subscribe('request:send:done', spyDone);

    request.setHeaders(headers)
           .setUrl('backbee/core/db/send-ok');

    requestHandler.send(request)
        .done(function (data, response, requestRes) {
            assert.deepEqual(typeof data, 'object', 'Data is in first param');
            assert.ok(response instanceof Response, 'Response object is in second param');
            assert.ok(requestRes instanceof Request, 'Request object is in third param');
            assert.deepEqual(request, requestRes, 'Request in param is equal to builded request');
            assert.deepEqual(response.getHeaders(), headers, 'Headers is correctly set to response');
            assert.deepEqual(response.getStatus(), 200, 'Status of response is 200');
            assert.deepEqual(response.getStatusText(), 'success', 'Status text of response is success');
            assert.deepEqual(response.getData(), {'foo': 1}, 'Data of response is set');
            assert.deepEqual(response.getRawData(), '{ \"foo\": 1 }', 'Raw data of response is set');
            assert.deepEqual(response.getErrorText(), '', 'Error text of response is empty');

            assert.ok(spy.calledWith(request), 'event request:send:before is called with the request');
            assert.ok(spyDone.calledWith(response), 'event request:send:done is called with the response and request');
            assert.ok(spy.calledBefore(spyDone), 'Event request:send:before is called before event request:send:done');

            done();
        }).fail(function () {
            assert.ok(false, 'Send function must work');
            done();
        });

    QUnit.server.respond();
});

QUnit.test('Test send function fail', function (assert) {
    var request = new Request(),
        done = assert.async(),
        headers = {'Content-Type': 'application/json'},
        spy = this.spy();

    mediator.subscribe('request:send:fail', spy);

    request.setHeaders(headers)
           .setUrl('backbee/core/db/send-nok');

    requestHandler.send(request)
        .done(function () {
            assert.ok(false, 'Send function must fail when server return 404');
            done();
        }).fail(function (response, requestRes) {
            assert.ok(response instanceof Response, 'Response object is in first param');
            assert.ok(requestRes instanceof Request, 'Request object is in second param');
            assert.deepEqual(request, requestRes, 'Request in param is equal to builded request');
            assert.deepEqual(response.getHeaders(), headers, 'Headers is correctly set to response');
            assert.deepEqual(response.getStatus(), 404, 'Status of response is 404');
            assert.deepEqual(response.getStatusText(), 'error', 'Status text of response is error');
            assert.deepEqual(response.getData(), {}, 'Data of response is set');
            assert.deepEqual(response.getRawData(), '', 'Raw data of response is set');
            assert.deepEqual(response.getErrorText(), 'Not Found', 'Error text of response is Not Found');

            assert.ok(spy.calledWith(response), 'event request:send:fail is called with the response');

            done();
        });

    QUnit.server.respond();
});

QUnit.test('Test simple request build', function (assert) {
    var done = assert.async();

    requestHandler.read('page').done(function () {
        assert.ok(true, 'Build and do correct request');
        done();
    }).fail(function () {
        assert.ok(false, 'This test must work');
        done();
    });
});

QUnit.test('Test request build with criteria uid', function (assert) {
    var done = assert.async(),
        criteria = {
            'uid': 'e07063113519d1c9e89788de47b955f3'
        };

    requestHandler.read('page', criteria).done(function (data, response, request) {

        assert.deepEqual(request.getUrl(), 'backbee/page/' + criteria.uid, 'Url is correctly builded');
        assert.deepEqual(typeof data, 'object', 'Data is set');

        done();

        return response;
    }).fail(function () {
        assert.ok(false, 'This test must work');
        done();
    });
});

