QUnit.test('Test Post request', function (assert) {
    var done = assert.async(),
        data = {
            'title': 'Foo',
            'layout_uid': 'bar',
            'parent_uid': 'foobar'
        };

    requestHandler.create('page', data).done(function (dataParam, response, request) {

        assert.deepEqual(request.getUrl(), 'backbee/page', 'Url is correctly builded');
        assert.deepEqual(request.getMethod(), 'POST', 'Method POST used');
        assert.deepEqual(request.getData(), JSON.stringify(data), 'Data is correctly set into Request');
        assert.deepEqual(typeof dataParam, 'object', 'Data is set');

        done();

        return response;
    }).fail(function () {
        assert.ok(false, 'This test must work');
        done();
    });
});

