QUnit.test('Test Patch request', function (assert) {
    var done = assert.async(),
        criteria = {
            'uid': 'e07063113519d1c9e89788de47b955f3'
        },
        data = {
            'title': 'bar'
        },
        operation = [
            {
                'op': 'replace',
                'path': '/title',
                'value': 'bar'
            }
        ];

    requestHandler.patch('page', data, criteria).done(function (dataParam, response, request) {

        assert.deepEqual(request.getUrl(), 'backbee/page/e07063113519d1c9e89788de47b955f3', 'Url is correctly builded');
        assert.deepEqual(request.getMethod(), 'PATCH', 'Method PUT used');
        assert.deepEqual(request.getData(), JSON.stringify(operation), 'Data is correctly set into Request');
        assert.deepEqual(typeof dataParam, 'object', 'Data is set');

        done();

        return response;
    }).fail(function () {
        assert.ok(false, 'This test must work');
        done();
    });
});


