'use strict';

var genericTestForm = require('./genericTestForm.spec');

QUnit.module('Form Builder Textarea element Tests');

QUnit.test('Work', function (assert) {
    var config = {
            'elements': {
                'mytextarea': {
                    'type': 'textarea',
                    'value': 'foo',
                    'cols': 22,
                    'rows': 3
                }
            }
        },
        data = {'mytextarea': 'foo'},
        object = genericTestForm(config, data, assert),
        $domForm = object.domForm,
        $wrap = $domForm.find('div.element_mytextarea'),
        $input = $wrap.find('textarea[name="mytextarea"]');

    assert.deepEqual($wrap.length, 1, 'Element found');
    assert.deepEqual($input.length, 1, 'Input found');
    assert.deepEqual($input.val(), config.elements.mytextarea.value, 'Value found');
    assert.deepEqual(parseInt($input.attr('cols'), 10), config.elements.mytextarea.cols, 'Cols found');
    assert.deepEqual(parseInt($input.attr('rows'), 10), config.elements.mytextarea.rows, 'Rows found');
});


