'use strict';

var jQuery = require('jquery');

var ContentManager = require('./../../../../../src/bundles/content/component/ContentManager');
var ContentContainer = require('./../../../../../src/bundles/content/component/ContentContainer');
var DefinitionManager = require('./../../../../../src/bundles/content/component/DefinitionManager');
var Content = require('./../../../../../src/bundles/content/model/Content');
var ContentSet = require('./../../../../../src/bundles/content/model/ContentSet');
var ContentDataGenerator = require('./../../../helpers/contentDataGenerator');

var Container = require('./../../../../../src/core/container');

QUnit.module('Test bundle content: Component - ContentManager');

QUnit.test('Content manager have default functions', function (assert) {
    assert.ok(typeof ContentManager.buildElement, 'Content manager have buildElement function');
});

QUnit.test('Build element function throw some errors', function (assert) {
    assert.throws(ContentManager.buildElement, new Error('Config must be set in ContentManager::buildElement'), 'Throw error when config is not set');
    assert.throws(ContentManager.buildElement.bind(undefined, {
        'uid': 'foo',
        'type': 'toto'
    }), new Error('Definition cannot be null in ContentManager::buildElement'), 'Error thrown when definition is not found');
});

QUnit.test('Test build element function', function (assert) {

    DefinitionManager.add(ContentDataGenerator.generateDefinitions());

    var content = ContentManager.buildElement({
            'uid': '997cbc1e2457408dbdd0d208b13a4de7',
            'type': 'Media/Image'
        }),
        content2 = ContentManager.buildElement({
            'id': content.id
        }),
        content3 = ContentManager.buildElement({
            'id': 'foo'
        }),
        contentSet = ContentManager.buildElement({
            'uid': 'b38e54fa29d6328f3600d8cb1c6bc86d',
            'type': 'ContentSet'
        });

    assert.ok(content instanceof Content, 'Element builded is an Content');
    assert.deepEqual(content, ContentContainer.get(content.id), 'Content is in content container');

    assert.deepEqual(content, content2, 'Element is correctly not builded');

    assert.deepEqual(content3, null, 'Element not found');

    assert.ok(contentSet instanceof ContentSet, 'Element builded is an ContentSet');
    assert.deepEqual(contentSet, ContentContainer.get(contentSet.id), 'ContentSet is in content container');

    DefinitionManager.clear();
});

QUnit.test('Create element function throw error when type is not set', function (assert) {
    assert.throws(ContentManager.createElement, new Error('Type must be set in ContentManager::createElement'), 'Error is correctly thrown');
});

QUnit.test('Test create element function', function (assert) {

    DefinitionManager.add(ContentDataGenerator.generateDefinitions());

    var type = 'Media/Image',
        done = assert.async();

    Container.set('page.uid', '123456');

    ContentManager.createElement(type).done(function (content) {

        assert.ok(content instanceof Content, 'A new content is created');
        assert.deepEqual(content.type, type, 'Type is ok');

        done();

        DefinitionManager.clear();
    }).fail(function () {
        assert.ok(false, 'This test must work');

        done();
    });

    assert.ok(true);
});

QUnit.test('Test getContentByNode function', function (assert) {

    DefinitionManager.add(ContentDataGenerator.generateDefinitions());

    assert.throws(ContentManager.getContentByNode, new Error('Node must be an instance of jQuery in ContentManager::getContentByNode'), 'Error thrown when node is not set');

    var $content = jQuery('<div data-bb-id="foo" data-bb-identifier="Media/Image(997cbc1e2457408dbdd0d208b13a4de8)"></div>'),
        content = ContentManager.getContentByNode($content),
        content2 = ContentManager.buildElement({
            'uid': '997cbc1e2457408dbdd0d208b13a4de8',
            'type': 'Media/Image'
        }),
        $content2 = jQuery('<div data-bb-id="bar"></div>');

    assert.deepEqual(content.uid, '997cbc1e2457408dbdd0d208b13a4de8', 'new Content is builded');

    $content.data('bb-id', content2.id);

    assert.deepEqual(ContentManager.getContentByNode($content), content2, 'Element is retrieved by id');

    assert.throws(ContentManager.getContentByNode.bind(ContentManager, $content2), new Error('Node is not a content in ContentManager::getContentByNode'), 'Error thrown when element is not compatible for content');

    DefinitionManager.clear();
});

QUnit.test('Test remove function', function (assert) {
    DefinitionManager.add(ContentDataGenerator.generateDefinitions());

    var $fixture = jQuery('#qunit-fixture'),
        content1,
        content2,
        contentSet;

    $fixture.html(
        '<div id="my-site">' +
            '<div class="bb-content rootContentSet" data-bb-identifier="ContentSet(e957e5f2958f1a3452d62bb9abf8e81d)">' +
                '<div class="bb-content cloudcontentset" data-bb-identifier="CloudContentSet(54621b46c1664db5ba7127d8f22aff00)">' +
                    '<div class="container">' +
                        '<div class="row">' +
                            '<div class="col-md-6">' +
                                '<div class="bb-content colcontentset bb-droppable bb-dnd" data-bb-identifier="ColContentSet(8319610f60fc1733fb789f0e3bdda9ab)">' +
                                    '<div class="bb-content" id="content" data-bb-identifier="Media/Image(997cbc1e2457408dbdd0d208b13a4de8)"></div>' +
                                '</div>' +
                            '</div>' +
                            '<div class="col-md-6">' +
                                '<div class="bb-content colcontentset bb-droppable bb-dnd" data-bb-identifier="ColContentSet(8319610f60fc1733fb789f0e3bdda9ab)">' +
                                    '<div class="bb-content" id="content2" data-bb-identifier="Media/Image(d82c8d1619ad8176d665453cfb2e55f0)"></div>' +
                                '</div>' +
                            '</div>' +
                        '</div>' +
                    '</div>' +
                '</div>' +
            '</div>' +
            '</div>'
    );

    content1 = ContentManager.getContentByNode($fixture.find('#content'));
    content2 = ContentManager.getContentByNode($fixture.find('#content2'));
    contentSet = ContentManager.getContentByNode($fixture.find('.cloudcontentset'));

    ContentManager.remove(content1);

    assert.deepEqual(ContentContainer.get(content1.id), null, 'Content is removed from container');
    assert.deepEqual($fixture.find('#content').length, 0, 'Content removed from dom');
    assert.deepEqual(contentSet.getChildren().length, 1, 'ContentSet have one child');

    ContentManager.remove(content2);
    assert.deepEqual(ContentContainer.get(content2.id), null, 'Content is removed from container');
    assert.deepEqual($fixture.find('#content2').length, 0, 'Content removed from dom');
    assert.deepEqual($fixture.find('.cloudcontentset').length, 0, 'ContentSet is removed from dom cause it dont have children');
});

QUnit.test('Test getPosition function', function (assert) {
    var $fixture = jQuery('#qunit-fixture');

    $fixture.html(
        '<div id="my-site">' +
            '<div class="bb-content rootContentSet" data-bb-identifier="ContentSet(e957e5f2958f1a3452d62bb9abf8e81d)">' +
                '<div id="h1" class="horizontal cloud-dnd ui-droppable""></div>' +
                '<div class="bb-content" id="contentset" data-bb-identifier="ContentSet(54621b46c1664db5ba7127d8f22aff00)">' +
                    '<div class="container">' +
                        '<div class="row">' +
                            '<div id="v1" class="vertical cloud-dnd ui-droppable"></div>' +
                            '<div class="col-md-6">' +
                                '<div class="bb-content" id="content" data-bb-identifier="Media/Image(997cbc1e2457408dbdd0d208b13a4de8)"></div>' +
                            '</div>' +
                            '<div id="v2" class="vertical cloud-dnd ui-droppable"></div>' +
                            '<div class="col-md-6">' +
                                '<div class="bb-content" id="content" data-bb-identifier="Media/Image(d82c8d1619ad8176d665453cfb2e55f0)"></div>' +
                            '</div>' +
                            '<div id="v3" class="vertical cloud-dnd ui-droppable"></div>' +
                        '</div>' +
                    '</div>' +
                '</div>' +
                '<div id="h2" class="horizontal cloud-dnd ui-droppable""></div>' +
            '</div>' +
            '</div>'
    );

    assert.deepEqual(ContentManager.getPosition(jQuery('#h1')), 0, 'Position is ok');
    assert.deepEqual(ContentManager.getPosition(jQuery('#h2')), 1, 'Position is ok');
    assert.deepEqual(ContentManager.getPosition(jQuery('#v1')), 0, 'Position is ok');
    assert.deepEqual(ContentManager.getPosition(jQuery('#v2')), 1, 'Position is ok');
    assert.deepEqual(ContentManager.getPosition(jQuery('#v3')), 2, 'Position is ok');
});

QUnit.test('Test refreshImages function', function (assert) {
    var $html = jQuery('<div><img src="test.jpg" /></div>');

    ContentManager.refreshImages($html, true);

    assert.ok($html.find('img').attr('src').length > 'test.jpg'.length, 'Image is refreshed');
});


