'use strict';

var jQuery = require('jquery');

var mediator = require('./../../../../src/core/mediator');
var container = require('./../../../../src/core/container');

var PopinManager = require('./../../../../src/services/popin');
var Popin = require('./../../../../src/services/popin/popin');

QUnit.module('Test popin service');

QUnit.test('Popin manager have default function', function (assert) {
    assert.ok(typeof PopinManager.createPopin, 'function', 'Popin manager have createPopin function');
});

QUnit.test('Popin manager createPopin', function (assert) {

    var popin = PopinManager.createPopin({
        title: 'Popin title',
        maxHeight: 500,
        appendTo: 'body'
    });

    assert.ok(popin instanceof Popin, 'A popin was created');
});

QUnit.test('Test overlay', function (assert) {

    jQuery('#qunit-fixture').html('<div id="cloud-ui"></div><div id="site-wrapper"></div>');

    container.set('$siteWrapper', jQuery('#site-wrapper'));

    var spy = this.spy();

    mediator.subscribe('overlay:click', spy);

    PopinManager.showOverlay();

    assert.deepEqual(jQuery('.cloud-overlay').length, 1, 'An overlay is shown');

    jQuery('.cloud-overlay').click();

    assert.ok(spy.called, 'Event is fired');

    PopinManager.hideOverlay();

    assert.ok(jQuery('.cloud-overlay').hasClass('cloud-hidden'), 'Overlay is hidden');

    PopinManager.showOverlay(true);

    assert.ok(jQuery('.cloud-overlay').hasClass('overlay-bg'), 'Overlay have a background');
    assert.notOk(jQuery('.cloud-overlay').hasClass('cloud-hidden'), 'Overlay dont have cloud-hidden class');

    PopinManager.hideOverlay();

    assert.notOk(jQuery('.cloud-overlay').hasClass('overlay-bg'), 'Overlay dont have a background');
    assert.ok(jQuery('.cloud-overlay').hasClass('cloud-hidden'), 'Overlay is hidden');
});



