'use strict';

var Fb = require('./../../../../src/services/form');

QUnit.module('Form Builder Tests');

QUnit.test('Default elements are registred', function (assert) {
    assert.ok(Fb.elements.text, 'Element Text is found');
    assert.ok(Fb.elements.textarea, 'Element Textarea is found');
    assert.ok(Fb.elements.checkbox, 'Element Checkbox is found');
    assert.ok(Fb.elements.radio, 'Element Radio is found');
    assert.ok(Fb.elements.hidden, 'Element Hidden is found');
    assert.ok(Fb.elements.select, 'Element Select is found');
    assert.ok(Fb.elements.submit, 'Element Submit is found');
    assert.ok(Fb.elements.password, 'Element Password is found');
});

QUnit.test('Default Fb functions are defined', function (assert) {
    assert.deepEqual(typeof Fb.getForm, 'function', 'getForm function is found');
    assert.deepEqual(typeof Fb.register, 'function', 'register function is found');
    assert.deepEqual(typeof Fb.setDefaultConfig, 'function', 'setDefaultConfig function is found');
    assert.deepEqual(typeof Fb.getDefaultConfig, 'function', 'getDefaultConfig function is found');
    assert.deepEqual(typeof Fb.register, 'function', 'register function is found');
    assert.deepEqual(typeof Fb.unRegister, 'function', 'unregister function is found');

    assert.deepEqual(Object.keys(Fb).length, 9, '9 attributes form the Fb');
});

QUnit.test('setDefaultConfig and getDefaultConfig functions work', function (assert) {
    var defaultConfig = {
        'elements': {
            'text': {
                'element_Class': 'foo',
                'wrap_class': 'bar'
            },
            'select': {
                'element_class': 'bar',
                'wrap_class': 'foo'
            }
        }
    };

    Fb.setDefaultConfig(defaultConfig);
    assert.deepEqual(Fb.getDefaultConfig(), defaultConfig, 'Default config is set');
    Fb.setDefaultConfig({});
});

QUnit.test('getForm function throw errors', function (assert) {
    assert.throws(Fb.getForm, new Error('Config must be set'), 'getForm function throw an error when config argument is missing');

    try {
        Fb.getForm({});
        assert.ok(false, 'config argument must have "elements" attibute');
    } catch (e) {
        assert.ok(true, 'getForm function throw an error when config dont have elements attibute');
        assert.deepEqual('Elements must be set', e.message);
    }
});

QUnit.test('New element is registred', function (assert) {
    Fb.register('foo', function () {
        return;
    });

    assert.ok(Fb.elements.foo, 'Element foo is found');
});

QUnit.test('Element is unregistred', function (assert) {
    Fb.unRegister('foo');

    assert.deepEqual(Fb.elements.foo, undefined, 'Element foo is not found');
});


