'use strict';

var ObjectIdentifier = require('./../../../../src/services/objectIdentifier');

QUnit.module('Test object identifier service');

QUnit.test('Test getObject function', function (assert) {
    assert.throws(ObjectIdentifier.getObject, new Error('Object identity not found in ObjectIdentifier::getObject'), 'Error thrown when object identity is not set');
    assert.deepEqual(ObjectIdentifier.getObject('aze'), null, 'Return null when object identity is not correct');
    assert.deepEqual(ObjectIdentifier.getObject('Foo(1)'), {'type': 'Foo', 'uid': '1'}, 'Object is correctly retrieved');
    assert.deepEqual(ObjectIdentifier.getObject('Foo/Bar(2)'), {'type': 'Foo/Bar', 'uid': '2'}, 'Object is correctly retrieved');
});

QUnit.test('Test build function', function (assert) {
    assert.throws(ObjectIdentifier.build, new Error('type or id missing on ObjectIdentifier::build'), 'Error thrown when classname or id is missing');
    assert.deepEqual(ObjectIdentifier.build('Foo', 1), 'Foo(1)', 'Object identifier is correctly builded');
});



