'use strict';

var Revision = require('./../../../../../src/bundles/content/model/Revision');

QUnit.module('Test bundle content: Model Revision');

QUnit.test('Revision throw errors if config is not correct', function (assert) {

    var revision;

    try {
        revision = new Revision();
        assert.ok(false, 'Revision must be throw exception if config is not set');
    } catch (e) {
        assert.deepEqual(e.message, 'Config must be set', 'Revision throw error when config is not set');
    }

    try {
        revision = new Revision({});
        assert.ok(false, 'Revision must be throw exception if uid is not set');
    } catch (e) {
        assert.deepEqual(e.message, 'Uid must be set', 'Revision throw error when uid is not set');
    }

    try {
        revision = new Revision({'uid': 'foo'});
        assert.ok(false, 'Revision must be throw exception if type is not set');
    } catch (e) {
        assert.deepEqual(e.message, 'Type must be set', 'Revision throw error when type is not set');
    }

    return revision;
});

QUnit.test('Revision construction', function (assert) {
    var revision = new Revision({
        'uid': 'Foo',
        'type': 'Bar'
    });

    assert.deepEqual(revision.uid, 'Foo', 'Abstract have a correct uid');
    assert.deepEqual(revision.type, 'Bar', 'Abstract have a correct type');
    assert.deepEqual(revision.elements, null, 'Revision have null elements by default');
    assert.deepEqual(revision.parameters, null, 'Revision have null parameters by default');
});

QUnit.test('Revision test setElements/removeElements function', function (assert) {
    var revision = new Revision({
            'uid': 'Foo',
            'type': 'Bar'
        }),
        elements = {
            'foo': 'Foo'
        };

    assert.deepEqual(revision.elements, null, 'Revision have null elements by default');

    revision.setElements(elements);

    assert.deepEqual(revision.elements, elements, 'setElements function work');

    revision.removeElements();

    assert.deepEqual(revision.elements, null, 'removeElements function work');
});

QUnit.test('Revision test addElement/getElement function', function (assert) {
    var revision = new Revision({
        'uid': 'Foo',
        'type': 'Bar'
    });

    revision.addElement('foo', 'Foo');

    assert.deepEqual(revision.getElement('foo'), 'Foo', 'addElement/getElement function work');
});

QUnit.test('Revision test setParameters/removeParameters function', function (assert) {
    var revision = new Revision({
            'uid': 'Foo',
            'type': 'Bar'
        }),
        parameters = {
            'foo': 'Foo'
        };

    assert.deepEqual(revision.parameters, null, 'Revision have null parameters by default');

    revision.setParameters(parameters);

    assert.deepEqual(revision.parameters, parameters, 'setParameters function work');

    revision.removeParameters();

    assert.deepEqual(revision.parameters, null, 'removeParameters function work');
});

QUnit.test('Revision test addParameter/getParameter function', function (assert) {
    var revision = new Revision({
        'uid': 'Foo',
        'type': 'Bar'
    });

    revision.addParameter('foo', 'Foo');

    assert.deepEqual(revision.getParameter('foo'), 'Foo', 'addParameter/getParameter function work');
});

QUnit.test('Revision test isEmpty function', function (assert) {
    var revision = new Revision({
        'uid': 'Foo',
        'type': 'Bar'
    });

    assert.ok(revision.isEmpty(), 'isEmpty function return true if parameters and elements are empty');

    revision.setElements({'foo': 'Foo'});

    assert.notOk(revision.isEmpty(), 'isEmpty function work');
});


