'use strict';

var jQuery = require('jquery'),
    mediator = require('./../../../../src/core/mediator'),
    container = require('./../../../../src/core/container'),
    structure = require('./../../../../src/services/structure');

QUnit.module('Test structure service', {
    beforeEach: function () {
        jQuery('#qunit-fixture').html('<div id="cloud-ui"></div>');
    },
    afterEach: function () {
        container.clear();
    }
});

QUnit.test('Structure have default function', function (assert) {
    assert.deepEqual(typeof structure.init, 'function', 'Structure have init function');
});

QUnit.test('Test init function', function (assert) {

    jQuery('#qunit-fixture').html('<div id="cloud-ui"></div>');

    container.set('$toolbar', jQuery('#cloud-ui'));

    var spy = this.spy(),
        $cloud;

    mediator.subscribe('structure:ready', spy);

    structure.init();
    $cloud = container.get('$toolbar');

    assert.deepEqual($cloud.find('.cloud-logo').length, 1, 'Structure init logo html');
    assert.deepEqual($cloud.find('#cloud-nav-primary').length, 1, 'Structure init primary nav html');
    assert.deepEqual($cloud.find('#cloud-nav-secondary').length, 1, 'Structure init secondary nav html');
    assert.deepEqual($cloud.find('.cloud-panel').length, 1, 'Structure init small panel');
    assert.ok($cloud.find('.cloud-panel').hasClass('panel-out'), 'Structure init small panel with hidden class');
    assert.deepEqual($cloud.find('.cloud-panel-fullpage').length, 1, 'Structure init full panel');
    assert.ok($cloud.find('.cloud-panel-fullpage').hasClass('cloud-hidden') && $cloud.find('.cloud-panel-fullpage').hasClass('panel-out'), 'Structure init full panel with hidden class');

    assert.deepEqual(typeof container.get('$toolbar'), 'object', 'Structure storage toolbar element');

    assert.ok(spy.called, 'Structure dispatch event "structure:end"');
});



