'use strict';

var cdg = require('./helpers/contentDataGenerator');

QUnit.server = sinon.fakeServer.create();

QUnit.server.autoRespond = true;

//Core Rest
QUnit.server.respondWith('GET', 'backbee/core/db/send-ok', [200, { 'Content-Type': 'application/json'}, '{ "foo": 1 }']);
QUnit.server.respondWith('GET', 'backbee/core/db/send-nok', [404, { 'Content-Type': 'application/json'}, '']);

//Build request with core/db Examples
QUnit.server.respondWith('GET', 'backbee/page', [200, { 'Content-Type': 'application/json'}, '{ "foo": 1 }']);
QUnit.server.respondWith('GET', 'backbee/page/e07063113519d1c9e89788de47b955f3', [200, { 'Content-Type': 'application/json'}, '{ "foo": 1 }']);
QUnit.server.respondWith('GET', 'backbee/page?parent_uid=foo&layout_uid=bar', [200, { 'Content-Type': 'application/json'}, '{ "foo": 1 }']);
QUnit.server.respondWith('GET', encodeURI('backbee/page?order_by[title]=ASC'), [200, { 'Content-Type': 'application/json'}, '{ "foo": 1 }']);
QUnit.server.respondWith('POST', 'backbee/page', [200, { 'Content-Type': 'application/json'}, '{}']);
QUnit.server.respondWith('DELETE', 'backbee/page/e07063113519d1c9e89788de47b955f3', [200, { 'Content-Type': 'application/json'}, '{}']);
QUnit.server.respondWith('PUT', 'backbee/page/e07063113519d1c9e89788de47b955f3', [200, { 'Content-Type': 'application/json'}, '{}']);
QUnit.server.respondWith('LINK', 'backbee/page/e07063113519d1c9e89788de47b955f3', [200, { 'Content-Type': 'application/json'}, '{}']);
QUnit.server.respondWith('PATCH', 'backbee/page/e07063113519d1c9e89788de47b955f3', [200, { 'Content-Type': 'application/json'}, '{}']);

//Authentication
QUnit.server.respondWith('POST', '/rest/2/security/authentication', function (xhr) {
    xhr.autoRespond = false;
    var validUserName = "backbeecloud",
        validPassword = "test",
        responseStatus = 401,
        responseHeaders = {},
        responseBody = "Invalid authentication informations",
        requestBody = JSON.parse(xhr.requestBody);

    if ((requestBody.username === validUserName) &&  (requestBody.password === validPassword)) {

        responseStatus = 201;
        responseHeaders = {
            'Content-Type': 'application/json',
            'X-API-KEY': 'qsdklfjkqsdflqsdfhl',
            'X-API-SIGNATURE': 'sdfdfsdfqsdfdf'
        };
        responseBody = '{}';
    }

    return xhr.respond(responseStatus, responseHeaders, responseBody);
});

QUnit.server.respondWith('DELETE', 'backbee/security/session', [200, { 'Content-Type': 'application/json'}, '{}']);

//Test content application
QUnit.server.respondWith('GET', 'backbee/classcontent/Media/Image/997cbc1e2457408dbdd0d208b13a4de7', [200, { 'Content-Type': 'application/json'}, JSON.stringify(cdg.generate())]);
QUnit.server.respondWith('GET', 'backbee/classcontent?definition=', [200, { 'Content-Type': 'application/json'}, JSON.stringify(cdg.generateDefinitions())]);
QUnit.server.respondWith('GET', 'backbee/classcontent?uids=997cbc1e2457408dbdd0d208b13a4de7,d24fd3ec8518e6e43edab9f07dbb7182&site_uid=', [200, { 'Content-Type': 'application/json'}, JSON.stringify(cdg.generates(2))]);
QUnit.server.respondWith('GET', 'backbee/classcontent-category?page_uid=123456', [200, { 'Content-Type': 'application/json'}, JSON.stringify(cdg.generateCategories())]);
QUnit.server.respondWith('GET', 'backbee/classcontent/Media/Image/997cbc1e2457408dbdd0d208b13a4de7?concise=', [200, { 'Content-Type': 'application/json'}, JSON.stringify(cdg.generate())]);
QUnit.server.respondWith('GET', 'backbee/classcontent/Media/Image/997cbc1e2457408dbdd0d208b13a4de7?info=', [200, { 'Content-Type': 'application/json'}, JSON.stringify(cdg.generateInfo())]);

//getHTML
QUnit.server.respondWith('GET', 'backbee/classcontent/Media/Image/137efc1e5648398daed0d208b13a4ae3?page_uid=123456', [200, { 'Content-Type': 'application/x-www-form-uriencoded', 'Accept': 'text/html'}, cdg.generateHTML()]);
QUnit.server.respondWith('GET', 'backbee/classcontent/Media/Image/137efc1e5648398daed0d208b13a4ae3', [200, { 'Content-Type': 'application/x-www-form-uriencoded', 'Accept': 'text/html'}, cdg.generateHTML()]);
QUnit.server.respondWith('GET', 'backbee/classcontent/Media/Image/137efc1e5648398daed0d208b13a4ae3?mode=foo&page_uid=pageuid', [200, { 'Content-Type': 'application/x-www-form-uriencoded', 'Accept': 'text/html'}, cdg.generateHTML()]);
QUnit.server.respondWith('GET', 'backbee/classcontent/Media/Image/4fded1464736e77865df232cbcb4cd19?mode=default', [200, { 'Content-Type': 'application/x-www-form-uriencoded', 'Accept': 'text/html'}, cdg.generateHTML()]);

//Save
QUnit.server.respondWith('POST', 'backbee/classcontent/Media/Image?page_uid=123456', function (xhr) {
    xhr.autoRespond = false;

    var responseHeaders = {
            'Content-Type': 'application/json',
            'BB-RESOURCE-UID': '4fded1464736e77865df232cbcb4cd19'
        };

    return xhr.respond(201, responseHeaders, '{}');
});

QUnit.server.respondWith('PUT', 'backbee/classcontent/Media/Image/d21fd909c4ce4c3ee7a6d84bf2bbb044?page_uid=123456', function (xhr) {
    xhr.autoRespond = false;

    var responseHeaders = {
            'Content-Type': 'application/json',
            'BB-RESOURCE-UID': '4fded1464736e77865df232cbcb4cd19'
        };

    return xhr.respond(201, responseHeaders, '{}');
});

QUnit.server.respondWith('PUT', 'backbee/classcontent/Media/Image/fd9d11e7706d0271eb3a2c943cb67f7d?page_uid=123456', function (xhr) {
    xhr.autoRespond = false;

    var responseHeaders = {
            'Content-Type': 'application/json',
            'BB-RESOURCE-UID': 'fd9d11e7706d0271eb3a2c943cb67f7d'
        };

    return xhr.respond(201, responseHeaders, '{}');
});


