'use strict';

var translator = require('./../../../src/core/translator'),
    mediator = require('./../../../src/core/mediator');

QUnit.module('Translator tests');

translator.setCatalog('en', require('../i18n/en_EN'));
translator.setCatalog('fr', require('../i18n/fr_FR'));

QUnit.test('Translator have default functions', function (assert) {
    assert.deepEqual(typeof translator.setLocale, 'function', 'Translator have setLocale function');
    assert.deepEqual(typeof translator.getLocale, 'function', 'Translator have getLocale function');
    assert.deepEqual(typeof translator.setCatalog, 'function', 'Translator have setCatalog function');
    assert.deepEqual(typeof translator.init, 'function', 'Translator have init function');
    assert.deepEqual(typeof translator.translate, 'function', 'Translator have translate function');
});

QUnit.test('Init, Translate work', function (assert) {
    assert.notEqual(translator.getLocale(), null, 'init set default lang without locale storage');
    translator.setLocale('xx');
    translator.init();
    assert.deepEqual(translator.translate('not_defined'), 'not_defined', 'Translate return the key when catalog is not defined');
    assert.deepEqual(translator.getLocale(), 'xx', 'init set default lang with locale storage: "xx"');
    translator.setLocale('en');
    assert.deepEqual(translator.getLocale(), 'en', 'Define locale "en"');
    assert.deepEqual(translator.translate('yes'), 'Yes', 'Translate return correct value "Yes" for key "yes"');
    assert.deepEqual(translator.translate('not_defined'), 'not_defined', 'Translate return the key when it is not defined');
    assert.deepEqual(translator.translate('results', {n: 4}), 'There are 4 results', 'Translate and replace one flag with a parameter');
    assert.deepEqual(translator.translate('pagination', {current: 4, total: 10}), '4/10', 'Translate and replace some flags with the parameters');
});


QUnit.test('Listen on:change:locale', function (assert) {
    mediator.subscribe('on:change:locale', translator.setLocale);
    mediator.publish('on:change:locale', 'fr');
    var userLang = translator.getLocale();
    assert.deepEqual(userLang, 'fr', 'Locale is defined: "fr"');
});



