'use strict';

var jQuery = require('jquery'),
    mediator = require('./../../../../src/core/mediator'),
    structure = require('./../../../../src/services/structure'),
    zone = require('./../../../../src/services/structure/zone'),
    container = require('./../../../../src/core/container'),
    menu = require('./../../../../src/services/structure/menu');

QUnit.module('Test zone service', {
    beforeEach: function () {
        jQuery('#qunit-fixture').html('<div id="cloud-ui"></div>');
        container.set('$toolbar', jQuery('#cloud-ui'));
        container.set('isMobile', false);
    },
    afterEach: function () {
        container.clear();
    }
});

QUnit.test('Zone have default functions', function (assert) {
    assert.deepEqual(typeof zone.init, 'function', 'Zone have init function');
    assert.deepEqual(typeof zone.hidePanels, 'function', 'Zone have hidePanels function');
    assert.deepEqual(typeof zone.setSmallPanel, 'function', 'Zone have setSmallPanel function');
    assert.deepEqual(typeof zone.setFullPanel, 'function', 'Zone have setFullPanel function');
    assert.deepEqual(typeof zone.addLayer, 'function', 'Zone have addLayer function');
    assert.deepEqual(typeof zone.closeLayer, 'function', 'Zone have closeLayer function');
});

QUnit.test('Test init function', function (assert) {
    structure.init();
    menu.init();

    try {
        zone.init();
        assert.ok(true, 'Init called without errors');
    } catch (e) {
        assert.ok(false, 'Init must be called without errors');
    }
});

QUnit.test('Test setSmallPanel function', function (assert) {

    structure.init();
    menu.init();
    zone.init();

    var title = 'publish',
        content = '<p id="foo">Foo</p>',
        $toolbar = jQuery('#cloud-ui'),
        $smallPanel = $toolbar.find('.cloud-panel'),
        done;

    assert.ok($smallPanel.hasClass('panel-out'), 'Small panel is hidden');

    zone.setSmallPanel(title, content);

    done = assert.async();

    setTimeout(function () {
        assert.notOk($smallPanel.hasClass('panel-out'), 'Small panel is not hidden after setted');
        assert.deepEqual($smallPanel.find('p.cloud-panel-title').text(), title, 'Small panel have a correct title');
        assert.deepEqual($smallPanel.find('div.cloud-panel-wrapper').find('#foo').length, 1, 'Small panel have a correct content');
        done();
    }, 500);
});

QUnit.test('Test a FullPanel', function (assert) {

    container.set('$toolbar', jQuery('#cloud-ui'));
    structure.init();
    menu.init();
    zone.init();

    var title = 'publish',
        content = '<p id="foo">Foo</p>',
        $toolbar = container.get('$toolbar'),
        $fullPanel = $toolbar.find('.cloud-panel-fullpage'),
        done;

    assert.ok($fullPanel.hasClass('cloud-hidden'), 'Full panel is hidden');

    zone.setFullPanel(title, content);

    done = assert.async();

    setTimeout(function () {
        assert.notOk($fullPanel.hasClass('cloud-hidden'), 'Full panel is not hidden after setted');
        assert.deepEqual($fullPanel.find('p.cloud-panel-title').text(), title, 'Full panel have a correct title');
        assert.deepEqual($fullPanel.find('div.cloud-panel-wrapper').find('#foo').length, 1, 'Full panel have a correct content');
        done();
    }, 500);
});

QUnit.test('Set small and full panels and click on "clone panels"', function (assert) {

    container.set('$toolbar', jQuery('#cloud-ui'));
    structure.init();
    menu.init();
    zone.init();

    var title = 'publish',
        content = '<p id="foo">Foo</p>',
        $smallPanel = container.get('$smallPanel'),
        $fullPanel = container.get('$fullPanel');

    assert.ok($smallPanel.hasClass('panel-out'), 'Small panel is hidden');
    assert.ok($fullPanel.hasClass('cloud-hidden'), 'Full panel is hidden');
    zone.setSmallPanel(title, content);
    zone.setFullPanel(title, content);

    setTimeout(function () {
        assert.ok($smallPanel.hasClass('animating') && !$smallPanel.hasClass('cloud-hidden'), 'Small panel is not hidden after setted');
        assert.ok($fullPanel.hasClass('animating') && !$fullPanel.hasClass('cloud-hidden'), 'Full panel is not hidden after setted');

        assert.deepEqual($smallPanel.find('p.cloud-panel-title').text(), title, 'Small panel have a correct title');
        assert.deepEqual($smallPanel.find('div.cloud-panel-wrapper').find('#foo').length, 1, 'Small panel have a correct content');
        assert.deepEqual($fullPanel.find('p.cloud-panel-title').text(), title, 'Full panel have a correct title');
        assert.deepEqual($fullPanel.find('div.cloud-panel-wrapper').find('#foo').length, 1, 'Small panel have a correct content');
    }, 500);

});

QUnit.test('Test addLayer and close a layer', function (assert) {

    structure.init();
    menu.init();
    zone.init();

    var title = 'publish',
        content = '<p>Foo</p>',
        layerTitle = 'Layer 1',
        layerContent = '<p id="foo">Layer</p>',
        layerTitle2 = 'Layer 2',
        layerContent2 = '<p id="foo">Bar</p>',
        $toolbar = container.get('$toolbar'),
        $smallPanel = $toolbar.find('.cloud-panel'),
        $layer,
        $layer2,
        done;

    zone.setSmallPanel(title, content);
    done = assert.async();

    setTimeout(function () {
        assert.ok($smallPanel.hasClass('animating') && !$smallPanel.hasClass('cloud-hidden'), 'Small panel is not hidden after setted');
        zone.addLayer(layerTitle, layerContent);
        setTimeout(function () {
            $layer = $smallPanel.find('div.cloud-layer-panel-wrapper').eq(0);
            assert.deepEqual($layer.length, 1, 'Layer exist');
            assert.deepEqual($layer.find('p.cloud-panel-title').text(), layerTitle, 'Layer have a correct title');
            assert.deepEqual($layer.find('.btn-close-layout').length, 1, 'Layer have a close btn');
            assert.deepEqual($layer.find('#foo').text(), 'Layer', 'Layer have the correct content');
            zone.addLayer(layerTitle2, layerContent2);
            setTimeout(function () {
                $layer2 = $smallPanel.find('div.cloud-layer-panel-wrapper').eq(1);
                assert.deepEqual($layer2.length, 1, 'Layer 2 exist');
                assert.deepEqual($layer2.find('p.cloud-panel-title').text(), layerTitle2, 'Layer have a correct title');
                assert.deepEqual($layer2.find('.btn-close-layout').length, 1, 'Layer have a close btn');
                assert.deepEqual($layer2.find('#foo').text(), 'Bar', 'Layer have the correct content');

                $layer2.find('.btn-close-layout').click();

                setTimeout(function () {
                    $layer2 = $smallPanel.find('div.cloud-layer-panel-wrapper').eq(1);
                    assert.deepEqual($layer2.length, 0, 'Layer 2 is removed');
                    done();
                }, 500);

            }, 500);
        }, 500);
    }, 500);

});



