'use strict';

var _ = require('lodash');
var jQuery = require('jquery');
var AbstractContent = require('./../../../../../src/bundles/content/model/AbstractContent');
var Revision = require('./../../../../../src/bundles/content/model/Revision');
var DefinitionManager = require('./../../../../../src/bundles/content/component/DefinitionManager');
var ContentDataGenerator = require('./../../../helpers/contentDataGenerator');

var getMockContent = function (configParam) {
    jQuery('#qunit-fixture').html('<div id="cloud-ui"><div class="bb-content"></div></div>');

    var config = {
            'uid': 'foo',
            'type': 'bar',
            '$': {},
            'definition': {
                'is_container': false,
                'properties': {}
            },
            'foo': 'Foo'
        },
        $content = jQuery('.bb-content');

    config.$ = $content;

    if (typeof configParam === 'object') {
        _.merge(config, configParam);
    }

    return {
        'object': new AbstractContent(config),
        'config': config,
        '$content': $content
    };
};

QUnit.module('Test bundle content: Model AbstractContent');

QUnit.test('AbstractContent throw errors if config is not correct', function (assert) {

    jQuery('#qunit-fixture').html('<div id="cloud-ui"><div class="bb-content"></div></div>');

    var abstractContent,
        $content = jQuery('.bb-content');

    try {
        abstractContent = new AbstractContent();
        assert.ok(false, 'AbstractContent must be throw exception if config is not set');
    } catch (e) {
        assert.deepEqual(e.message, 'Config must be set', 'AbstractContent throw error when config is not set');
    }

    try {
        abstractContent = new AbstractContent({});
        assert.ok(false, 'AbstractContent must be throw exception if uid is not set');
    } catch (e) {
        assert.deepEqual(e.message, 'Uid must be set', 'AbstractContent throw error when uid is not set');
    }

    try {
        abstractContent = new AbstractContent({'uid': 'Foo'});
        assert.ok(false, 'AbstractContent must be throw exception if type is not set');
    } catch (e) {
        assert.deepEqual(e.message, 'Type must be set', 'AbstractContent throw error when type is not set');
    }

    try {
        abstractContent = new AbstractContent({'uid': 'Foo', 'type': 'Bar', '$': $content});
        assert.ok(false, 'AbstractContent must be throw exception if definition is not set');
    } catch (e) {
        assert.deepEqual(e.message, 'Definition must be set', 'AbstractContent throw error when definition is not set');
    }

    return abstractContent;
});

QUnit.test('AbstractContent constuction', function (assert) {

    var mockContent = getMockContent(),
        abstractContent = mockContent.object,
        config = mockContent.config,
        $content = mockContent.$content,
        revision = abstractContent.revision;

    assert.deepEqual(abstractContent.uid, config.uid, 'AbstractContent have a correct uid');
    assert.deepEqual(abstractContent.type, config.type, 'AbstractContent have a correct type');
    assert.deepEqual(abstractContent.$, config.$, 'AbstractContent have a correct jquery object');
    assert.deepEqual(abstractContent.definition, config.definition, 'AbstractContent have a correct definition');
    assert.deepEqual(abstractContent.id.length, 6, 'AbstractContent have a string with 6 to length for id');
    assert.notOk(abstractContent.updated, 'AbstractContent have a updated to false by default');

    assert.ok(revision instanceof Revision, 'Abstract have an instance of Revision');
    assert.deepEqual(revision.uid, abstractContent.uid, 'Revision have same uid with content');
    assert.deepEqual(revision.type, abstractContent.type, 'Revision have same type with content');

    assert.deepEqual(abstractContent.foo, config.foo, 'AbstractContent retrieve all config in content object');

    assert.deepEqual($content.data('bb-id'), abstractContent.id, 'AbstractContent set id to the jQuery element');
});

