'use strict';

var genericTestForm = require('./genericTestForm.spec');

QUnit.module('Form Builder SelectButton element Tests');

QUnit.test('Work', function (assert) {
    var config = {
            'elements': {
                'myselectbutton': {
                    'type': 'selectButton',
                    'value': 'right',
                    'buttons': {
                        'left': {
                            'icon': 'icon-align-left'
                        },
                        'middle': {
                            'icon': 'icon-align-center'
                        },
                        'right': {
                            'icon': 'icon-align-right'
                        }
                    }
                }
            }
        },
        data = {'myselectbutton': 'right'},
        object = genericTestForm(config, data, assert),
        $domForm = object.domForm,
        $wrap = $domForm.find('div.element_myselectbutton');

    assert.deepEqual($wrap.find('button').length, 3, '3 buttons added');
    assert.ok($wrap.find('button[data-key="right"]').hasClass('selected'), 'Right is selected');
});


