'use strict';

var mediator = require('./../../../../../src/core/mediator');
var popinManager = require('./../../../../../src/services/popin');
var Popin = require('./../../../../../src/services/popin/popin');
var jQuery = require('jquery');

QUnit.module('Test Popin');

QUnit.test('Test Popin creation and properties', function (assert) {
    var popin = new Popin({title: 'My title'});

    assert.ok(popin.getId(), 'Id is not null');

    assert.deepEqual(popin.getChildren().length, 0, 'Popin have not children by default');

    assert.deepEqual(popin.getTitle(), 'My title', 'title property is retrieved properly');

    popin.setTitle('My new title');
    assert.deepEqual(popin.getTitle(), 'My new title', 'Title was changed by setTitle');

    assert.deepEqual(popin.getOption('title'), 'My new title', 'Title Property was readed with getOption');

    popin.setOption('maxHeight', 500);
    assert.deepEqual(popin.getOption('maxHeight'), 500, 'maxHeight Property was setted with setOption');

    assert.deepEqual(typeof popin.getOption(), 'object', 'Return all options');

    assert.deepEqual(popin.getOption('toto'), undefined, 'Option method return undefined if parameter doesnt exist');
});

QUnit.test('Test Popin invoke method (throws)', function (assert) {
    var popin = new Popin({title: 'My title'});

    assert.throws(popin.invoke, new Error('popin.invoke expects one parameter'), 'calling invoke without a parameter throws an exception');

    assert.throws(popin.invoke.bind(popin, 'blaze'),
        new Error('popin.invoke Error: no such method \'blaze\' for dialog widget instance'),
        'Invoking a method that doesn\'t exist throws an exception');
});

QUnit.test('Test Popin events setted with constructor', function (assert) {
    var spyOnCreate = this.spy(),
        spyOnOpen = this.spy(),
        spyOnClose = this.spy(),
        popinId,

        popin = new Popin({
            title: 'Popin title',
            maxHeight: 500,
            appendTo: '#qunit-fixture',
            create: spyOnCreate,
            open: spyOnOpen,
            close: spyOnClose
        });

    assert.ok(spyOnCreate.called, 'popin `create` event was triggered');

    assert.notOk(jQuery('#' + popin.getId()).is(':visible'), 'The dialog is not visible');
    popin.display();

    assert.ok(spyOnOpen.called, 'Popin `open` event was triggered');
    assert.ok(jQuery('#' + popin.getId()).is(':visible'), 'Popin is now visible');

    /* close */
    popin.close();
    assert.notOk(jQuery('#' + popin.getId()).is(':visible'), 'The dialog is hidden');

    assert.ok(spyOnClose.called, 'Popin `close` event was triggered');

    popinId = popin.getId();

    popin.destroy();

    assert.deepEqual(popin.getId(), null, 'Popin is destroyed now');

    assert.notOk(document.getElementById(popinId), 'Dialog is deleted from the Dom');
});

QUnit.test('Test Popin using the on method to add event listener', function (assert) {
    var aPopin = new Popin({
            appendTo: '#qunit-fixture'
        }),
        spyOnClose = this.spy(),
        spyOnOpen = this.spy();

    aPopin.on('open', spyOnOpen);
    aPopin.on('close', spyOnClose);

    aPopin.display();
    assert.ok(spyOnOpen.called, 'Popin `open` was triggered');

    aPopin.close();
    assert.ok(spyOnClose.called, 'Popin `close` was triggered');
});


QUnit.test('Test Popin child creation', function (assert) {

    var spyOnChildClose = this.spy(),
        spyOnParentDialogOnOpen = this.spy(),
        spyOnParentDialogOnClose = this.spy(),
        parentPopin,
        childPopin;

    parentPopin = new Popin({
        title : 'Parent popin',
        appendTo: '#qunit-fixture'
    });

    parentPopin.on('open', spyOnParentDialogOnOpen);
    parentPopin.on('close', spyOnParentDialogOnClose);

    assert.equal(parentPopin.getChildren().length, 0, 'Main popin has no child');

    childPopin = parentPopin.createChild({
        title: 'Popin with Parent',
        close: spyOnChildClose,
        appendTo: '#qunit-fixture'
    });

    assert.equal(parentPopin.getChildren().length, 1, 'Main popin has one child');

    parentPopin.display();
    childPopin.display();

    assert.ok(spyOnParentDialogOnOpen.called, 'Open event was triggered by main dialog');

    assert.ok(jQuery('#' + parentPopin.getId()).is(':visible'), 'main popin is now visible');
    assert.ok(jQuery('#' + childPopin.getId()).is(':visible'), 'child popin is now visible');

    parentPopin.close();

    assert.ok(spyOnParentDialogOnClose.called, 'Close event was triggered by main dialog');
    assert.ok(spyOnChildClose.called, 'Close event was called on childPopin');

});

QUnit.test('Test Popin content', function (assert) {

    var popin = new Popin({
            title : "My popin",
            appendTo: '#qunit-fixture'
        }),
        content = '<p class="popin-test-content">This is my content</p>';

    popin.setContent(content);

    assert.deepEqual(content, popin.getContent().html(), 'Popin content has been set');
});

QUnit.test('Test Popin display, hide and destroy', function (assert) {

    var popin = new Popin({
            title: 'My title',
            appendTo: '#qunit-fixture'
        }),
        popinId = popin.getId();

    assert.notOk(jQuery('#' + popinId).is(':visible'), 'Popin is not visible');

    popin.display();
    assert.ok(jQuery('#' + popinId).is(':visible'), 'Popin is visible');

    popin.hide(popin);

    assert.notOk(jQuery('#' + popinId).is(':visible'), 'Popin is hidden');

    popin.destroy(popin);

    assert.deepEqual(popin.getId(), null, 'Popin is destroyed now');
    assert.notOk(document.getElementById(popinId), 'Popin is deleted from the Dom');
});



