'use strict';

var genericTestForm = require('./genericTestForm.spec');
var jQuery = require('jquery');

QUnit.module('Form Builder Social element Tests');

QUnit.test('Work', function (assert) {
    var config = {
            'elements': {
                'mysocial': {
                    'type': 'social',
                    'label': 'Social',
                    'wrap_class': 'form-group',
                    'element_class': 'form-control',
                    'label_class': 'form-control',
                    'value': {
                        'facebook': {'placeholder': 'https://facebook.com/username', 'url': 'https://facebook.com/username', 'enable': true},
                        'twitter': {'placeholder': 'https://twitter.com/username', 'url': 'http://twitter.com', 'enable': true},
                        'instagram': {'placeholder': 'https://instagram.com/username', 'url': 'http://instagram.com', 'enable': true},
                        'googleplus': {'placeholder': 'https://plus.google.com/userid', 'url': 'http://googleplus.com', 'enable': true},
                        'linkedin': {'placeholder': 'https://www.linkedin.com/in/username-userid', 'url': 'http://linkedin.com', 'enable': true},
                        'flickr': {'placeholder': 'https://www.flickr.com/photos/userid', 'url': 'http://flickr.com', 'enable': true}
                    }
                }
            }
        },
        data = {
            'mysocial': {
                'facebook': {'placeholder': 'https://facebook.com/username', 'trans': 'social_facebook', 'url': 'https://facebook.com/username', 'enable': true},
                'twitter': {'placeholder': 'https://twitter.com/username', 'trans': 'social_twitter', 'url': 'http://twitter.com', 'enable': true},
                'instagram': {'placeholder': 'https://instagram.com/username', 'trans': 'social_instagram', 'url': 'http://instagram.com', 'enable': true},
                'googleplus': {'placeholder': 'https://plus.google.com/userid', 'trans': 'social_googleplus', 'url': 'http://googleplus.com', 'enable': true},
                'linkedin': {'placeholder': 'https://www.linkedin.com/in/username-userid', 'trans': 'social_linkedin', 'url': 'http://linkedin.com', 'enable': true},
                'flickr': {'placeholder': 'https://www.flickr.com/photos/userid', 'url': 'http://flickr.com', 'trans': 'social_flickr', 'enable': true}
            }
        },
        object = genericTestForm(config, data, assert),
        $domForm = object.domForm,
        $wrap = $domForm.find('div.element_mysocial');

    assert.deepEqual($wrap.length, 1, 'Element found');
    assert.deepEqual($wrap.find('input[type="checkbox"]').length, 6, '6 checkboxes added');
    assert.deepEqual($wrap.find('input[type="text"]').length, 6, '6 inputs added');

    assert.ok($wrap.hasClass('element_mysocial ' + config.elements.mysocial.wrap_class), 'Wrap class found');

    jQuery.each(data.mysocial, function (key, value) {
        var $input = $wrap.find('input[type="text"][name="' + key + '"]'),
            $checkbox = $wrap.find('input[type="checkbox"][name="' + key + '_check"]');

        assert.ok($checkbox.prop('checked'), 'Check if ' + key + ' checkbox is checked');
        assert.ok($input.hasClass(config.elements.mysocial.element_class), 'Input ' + key + ' class found');
        assert.ok(($input.val().length > 0), 'Check if ' + key + ' input has `' + value.url + '` value');
        assert.ok(($input.attr('placeholder') === value.placeholder), 'Check if ' + key + ' input has `' + value.placeholder + '` placeholder');
    });
});


