'use strict';

var flash = require('./../../../../src/services/flashMessage'),
    notifier = require('./../../../../src/services/notifier'),
    jQuery = require('jquery'),
    structure = require('./../../../../src/services/structure'),
    _ = require('lodash'),
    container = require('./../../../../src/core/container');

QUnit.module('Test Flash message service', {
    beforeEach: function () {
        localStorage.removeItem('flash');
        jQuery('#qunit-fixture').html('<div id="cloud-ui"></div>');
    }
});

QUnit.test('Flash have default functions', function (assert) {
    assert.deepEqual(typeof flash.add, 'function', 'Flash have add message function');
    assert.deepEqual(typeof flash.flash, 'function', 'Flash have flash message display function');
});

QUnit.test('Test Flash service', function (assert) {
    var message;

    assert.deepEqual(localStorage.getItem('flash'), null, 'Flash have not any message');

    flash.add('foo', 'success');
    message = JSON.parse(localStorage.getItem('flash'));
    assert.ok(_.isArray(message), 'Flash catalog is an array');
    assert.deepEqual(message.length, 1, 'One message is stored');
    assert.deepEqual(message[0].message, 'foo', 'Message content is correct');
    assert.deepEqual(message[0].type, 'success', 'Message type is correct');

    flash.add('bar', 'warning');
    message = JSON.parse(localStorage.getItem('flash'));
    assert.deepEqual(message.length, 2, 'Two messages are stored');
    assert.deepEqual(message[1].message, 'bar', 'Message content is correct');
    assert.deepEqual(message[1].type, 'warning', 'Message type is correct');

    flash.add('foo success', 'success');
    message = JSON.parse(localStorage.getItem('flash'));
    assert.deepEqual(message[2].type, 'success', 'Message type is correct');

    flash.add('bar error', 'error');
    message = JSON.parse(localStorage.getItem('flash'));
    assert.deepEqual(message[3].type, 'error', 'Message type is correct');

    flash.add('bar success', 'wrong_type');
    message = JSON.parse(localStorage.getItem('flash'));
    assert.deepEqual(message[4].type, 'info', 'Message type is fix by info notifier');

    flash.add('bar default');
    message = JSON.parse(localStorage.getItem('flash'));
    assert.deepEqual(message[4].type, 'info', 'Message is created with default type');

    flash.flash();
    message = JSON.parse(localStorage.getItem('flash'));
    assert.deepEqual(localStorage.getItem('flash'), null, 'Flash messages are deleted');

});


