'use strict';

const _ = require('lodash');

let registredEvents = {};

/**
 * @module core/mediator
 * @requires lodash
 * @author Florian Kroockmann <florian.kroockmann@lp-digital.fr>
 */
module.exports = {
  /**
   * Subscribe to an event
   *
   * @param  {String}   eventName
   * @param  {Function} callback
   * @param  {Boolean}  once
   */
  subscribe: function(eventName, callback, once = false) {
    registredEvents[eventName] = registredEvents[eventName] || [];

    registredEvents[eventName].push({'callback': callback, 'once': once});
  },

  /**
   * Publish an event
   *
   * @param  {String} eventName
   * @param  {Object} params
   * @param  {String} context
   */
  publish: function(eventName, params, context) {
    let events = registredEvents[eventName] ||[];

    events.forEach((event) => {
      if (event) {
        event.callback.call(context, params);
        if (event.once) {
          _.pull(events, event);
        }
      }
    });
  },

  /**
   * Remove events from the mediator
   *
   * @param  {Object|String} events
   */
  clear: function(events) {
    if (_.isString(events)) {
      events = [events];
    }

    if (events) {
      _.forEach(events, function(event) {
        delete registredEvents[event];
      });
    } else {
      registredEvents = {};
    }
  },
};



