'use strict';

var jQuery = require('jquery'),
    mediator = require('./../../../../src/core/mediator'),
    container = require('./../../../../src/core/container'),
    structure = require('./../../../../src/services/structure'),
    zone = require('./../../../../src/services/structure/zone'),
    menu = require('./../../../../src/services/structure/menu');

QUnit.module('Test menu service', {
    beforeEach: function () {
        jQuery('#qunit-fixture').html('<div id="cloud-ui"></div>');
    },
    afterEach: function () {
        container.clear();
        menu.clear();
    }
});

QUnit.test('Menu have default functions', function (assert) {
    assert.deepEqual(typeof menu.init, 'function', 'Menu have init function');
    assert.deepEqual(typeof menu.add, 'function', 'Menu have add function');
});

QUnit.test('Menu init dispatch event', function (assert) {
    var done = assert.async();

    mediator.subscribe('menu:ready', function (add) {
        assert.ok(true, 'Menu dispatch event "menu:ready" after init');
        assert.deepEqual(typeof add, 'function', 'The first param is an function for add item in menu');

        done();
    });

    menu.init();
});

QUnit.test('Menu throw errors if all params are not set', function (assert) {
    assert.throws(menu.add, new Error('You must set all params'), 'Menu throw error when all params is not set');
});

QUnit.test('Test add function', function (assert) {
    container.set('$toolbar', jQuery('#cloud-ui'));
    structure.init();
    menu.init();

    var nav = jQuery('#cloud-nav-primary'),
        spy = this.spy(),
        spy2 = this.spy(),
        spy3 = this.spy(),
        item1,
        item2,
        item3;

    item1 = menu.add('publish', 'publish', 10, 'publish', 'publish', true);
    assert.deepEqual(nav.find('li').length, 1, 'One item1 is added into the menu');
    assert.deepEqual(item1.html.find('i').hasClass('icon-publish'), true, 'Icon is correctly set to item1');
    assert.deepEqual(item1.html.find('span.cloud-label').text(), 'publish', 'Name is correcty set to item1');
    assert.deepEqual(item1.html.find('.btn').hasClass('btn-focus'), true, 'Item1 is highlighted cause the focus is set to true');

    mediator.subscribe('menu:publish:click', spy);
    item1.html.click();

    assert.deepEqual(item1.html.find('.btn').hasClass('btn-focus'), false, 'Item1 turn off and is not highlighted after click');

    item1.html.click();
    assert.deepEqual(item1.html.find('.btn').hasClass('btn-focus'), true, 'Item1 is highlighted after click');

    item2 = menu.add('new-page', 'new-page', 5, 'new-page');
    assert.deepEqual(nav.find('li').length, 2, 'Two items is already on the menu');
    assert.deepEqual(item2.html.find('i').hasClass('icon-new-page'), true, 'Icon is correctly set to item2');
    assert.deepEqual(item2.html.find('span.cloud-label').text(), 'new-page', 'Name is correcty set to item2');
    assert.deepEqual(item2.html.find('.btn').hasClass('btn-focus'), false, 'Item2 is not highlighted by default');

    assert.ok(item2.html.next().is(item1.html), 'Item2 is correctly add before item1');

    mediator.subscribe('menu:new-page:click', spy2);
    item2.html.click();

    assert.deepEqual(item1.html.find('.btn').hasClass('btn-focus'), false, 'Item1 is not highlighted after item2 click');
    assert.deepEqual(item2.html.find('.btn').hasClass('btn-focus'), true, 'Item2 is highlighted after click');

    item3 = menu.add('foo', 'foo', 7, 'foo');
    assert.deepEqual(nav.find('li').length, 3, 'Three items is already on the menu');
    assert.deepEqual(item3.html.find('i').hasClass('icon-foo'), true, 'Icon is correctly set to item3');
    assert.deepEqual(item3.html.find('span.cloud-label').text(), 'foo', 'Name is correcty set to item3');
    assert.deepEqual(item3.html.find('.btn').hasClass('btn-foo'), false, 'Item3 is not highlighted by default');

    assert.ok(item3.html.next().is(item1.html), 'Item3 is correctly add before item1');
    assert.ok(item3.html.prev().is(item2.html), 'Item3 is correctly add after item2');

    mediator.subscribe('menu:foo:click', spy3);
    item3.html.click();

    assert.deepEqual(item2.html.find('.btn').hasClass('btn-focus'), false, 'Item2 is not highlighted after item3 click');
    assert.deepEqual(item3.html.find('.btn').hasClass('btn-focus'), true, 'Item3 is highlighted after click');

    assert.ok(spy.calledOnce, 'Click on item1 dispatch menu:publish:click event');
    assert.ok(spy2.calledOnce, 'Click on item2 dispatch menu:new-page:click event');
    assert.ok(spy3.calledOnce, 'Click on item3 dispatch menu:foo:click event');
});

QUnit.test('Test nav secondary', function (assert) {
    container.set('isMobile', false);
    container.set('$toolbar', jQuery('#cloud-ui'));
    structure.init();
    menu.init();

    var nav = jQuery('#cloud-nav-primary'),
        navSecondary = jQuery('#cloud-nav-secondary');

    menu.add('publish', 'publish', 10, 'publish', 'publish', true);
    menu.add('publish', 'publish', 20, 'publish', 'publish', true);
    menu.add('secondary account', 'account', 90, 'account', 'account', true, undefined, 'secondary');

    assert.deepEqual(nav.find('li').length, 2, 'Nav primary have 1 link');
    assert.deepEqual(navSecondary.find('li').length, 1, 'Nav secondary have 1 link');


});

QUnit.test('Test clear function', function (assert) {
    container.set('isMobile', false);
    container.set('$toolbar', jQuery('#cloud-ui'));
    structure.init();
    menu.init();

    var nav = jQuery('#cloud-nav-primary'),
        spy = this.spy(),
        item1;

    item1 = menu.add('publish', 'publish', 10, 'publish', 'publish', true);

    mediator.subscribe('menu:publish:click', spy);
    item1.html.click();

    assert.deepEqual(nav.find('li').length, 1, 'One item on menu');

    menu.clear();

    assert.deepEqual(nav.find('li').length, 0, 'Items cleared');
});

QUnit.test('Test unFocus function', function (assert) {
    var item;

    container.set('isMobile', false);
    container.set('$toolbar', jQuery('#cloud-ui'));
    structure.init();
    menu.init();

    item = menu.add('publish', 'publish', 10, 'publish', null, true);
    assert.deepEqual(item.html.find('.btn').hasClass('btn-focus'), true, 'Item is highlighted');

    require('./../../../../src/services/structure/menu/desktop').unFocus();
    assert.deepEqual(item.html.find('.btn').hasClass('btn-focus'), false, 'Item is not highlighted, unFocus work');

});

QUnit.test('Test mobile menu', function (assert) {
    container.set('isMobile', true);
    container.set('$toolbar', jQuery('#cloud-ui'));
    structure.init();
    menu.init();

    var $toolbar = jQuery('#cloud-ui'),
        $menu,
        $btn,
        $dropback = container.get('$dropback'),
        $nav = jQuery('#cloud-nav-primary'),
        regexTransformY = new RegExp(/([0-9]+)\)/g),
        regexTransision = new RegExp(/([0-9.]+)s/),
        transformY,
        transisionDuration,
        done;

    $btn = $toolbar.find('.cloud-menu');
    $menu = $toolbar.find('.cloud-nav-wrapper');

    menu.add('publish', 'publish', 10, 'publish', null, true);

    assert.ok($dropback.hasClass('cloud-hidden'), 'DropBack is not visible');
    assert.deepEqual($btn.length, 1, 'Button exists');
    assert.deepEqual($nav.find('li').length, 1, 'One item is already on the menu');

    assert.deepEqual($toolbar.css('bottom'), '0px', 'Toolbar position bottom is 0px');
    assert.deepEqual($dropback.length, 1, 'Dropback is here');
    assert.deepEqual($menu.find('.menu-handler').length, 1, 'Handler is here');

    transformY = regexTransformY.exec($menu.css('transform'))[1];
    transisionDuration = regexTransision.exec($menu.css('transition'))[1] * 1000;

    assert.ok((transformY > 0), 'Navigation is hidden by a transform Y');

    done = assert.async();

    $btn.trigger(jQuery.Event('touchstart', { pageX: 200, pageY: 200 }));

    setTimeout(function () {
        transformY = regexTransformY.exec($menu.css('transform')) ? regexTransformY.exec($menu.css('transform'))[1] : 0;
        assert.ok((transformY === 0), 'Navigation is visible, transformY == 0');
        assert.deepEqual($dropback.css('opacity'), "1", 'Dropback is visible');

        assert.notOk($dropback.hasClass('cloud-hidden'), 'DropBack is visible');

        $dropback.trigger(jQuery.Event('touchend', {pageX: 200, pageY: 200}));

        setTimeout(function () {
            transformY = regexTransformY.exec($menu.css('transform'))[1];
            assert.ok((transformY > 0), 'Navigation is hidden by a transform Y');
            assert.ok($dropback.hasClass('cloud-hidden'), 'DropBack is not visible');

            done();
        }, transisionDuration + 50);

    }, transisionDuration + 300 + 50);
});

QUnit.test('Test mobile panels', function (assert) {
    container.set('isMobile', true);
    container.set('$toolbar', jQuery('#cloud-ui'));

    var done;

    structure.init();
    menu.init();
    zone.init();

    done = assert.async();

    zone.setSmallPanel('title', 'content');
    setTimeout(function () {
        zone.setFullPanel('title', 'content');
        assert.deepEqual(container.get('$toolbar').find('.cloud-layer-panel-wrapper').length, 1, 'full panel is setted in a layer');

        done();
    }, 500);
});



